import { AuthService } from '../../services/auth.service';
import { Component, Input } from '@angular/core';
import { LoginService } from '../../services/login.service';
import { Router } from '@angular/router';

@Component({
  selector: 'app-nav-menu',
  templateUrl: './nav-menu.component.html',
  styleUrls: ['./nav-menu.component.css']
})
export class NavMenuComponent {
  isExpanded = false;
  isLogged = false;
  logged: boolean;
  @Input() username: string;
  @Input() password: string;
  constructor(private authService: AuthService, private loginService: LoginService, private router: Router) {
    if (this.loginService.getLog() == false) {
      this.loginService.logged = false;
      this.logged = this.loginService.logged;
    }
    else {
      this.logged = true;
    }
  }

  urlFile = require('../../ressources/logo_Machtens.png');

  collapse() {
    this.isExpanded = false;
  }

  toggle() {
    this.isExpanded = !this.isExpanded;
  }

  hasRole() {
    return this.authService.getRole();
  }

  public message = "";
  //logged = null;

  async login() {
    await this.loginService.login(this.username, this.password, (logged) => {
      if (logged == true) {
        this.logged = true;
      }
    });

    //console.log(this.username + ' ' + this.password);
  }

  displayMessage(response: string) {
    this.message = response;
  }

  logout() {
    this.loginService.logout();
    this.loginService.logged = false;
    this.logged = false;
    this.router.navigateByUrl("/");
  }
}



