import { Component } from '@angular/core';
import { RequestService } from '../../../services/request.service';
import { User } from '../../../models/user';
import { Role } from '../../../models/role';
import { UserService } from '../../../services/user.service';
import { RoleService } from '../../../services/role.service';

@Component({
  selector: 'gestion-user-component',
  templateUrl: './gestion.user.html'
})
export class GestionUserComponent {
  oldUser = new User();
  user = new User();
  usersTab = new Array<User>();
  roles = new Array<Role>();
  constructor(private requestService: RequestService, private userService: UserService, private roleService: RoleService) {
    this.usersTab = new Array<User>();
    console.log("constructor");
    this.afficherUsers();
    this.user = new User();
    this.roleService.getAllRoles((roles) => {
      this.roles = roles;
    });
  }

  onSubmit() {
    this.userService.createUser(this.user, (user) => {
      this.afficherUsers();
    });
  }

  afficherUsers() {
    this.userService.getAllUsers((users) => {
      console.log("tableau copié...");
      this.usersTab = users;
    });
  }

  reinitialiser() {
    this.user = new User();
  }

  updateUser() {
    console.log("update User");
    this.userService.updateUser(this.user, this.oldUser, (rep) => {
      if (Object.keys(rep).length > 0) {
        this.afficherUsers();
      }
    });
  }

  modifier(i) {
    this.user = this.usersTab[i];
    this.oldUser = new User();
    this.oldUser = this.user;
  }

  supprimer(index) {
    this.userService.deleteUser(this.usersTab[index], (rep) => {
      if (Object.keys(rep).length > 0) {
        this.afficherUsers();
      }
    });
  }
}



