import { Injectable } from '@angular/core';
import { RequestService } from './request.service';
import { HttpClient } from '@angular/common/http';
import { Module } from '../models/module';
import { Section } from '../models/section';
import { ACompose } from '../models/aCompose';
import { SectionService } from './section.service';
import { ModuleService } from './module.service';

// SECTION AND MODULE
@Injectable()
export class AComposeService {

  aComposeTab = new Array<ACompose>();
  aCompose: ACompose = new ACompose();
  moduleFromSection = new Array<Module>();
  request: string;
  savedACompose: ACompose;
  saved = false;

  constructor(
    private requestService: RequestService,
    private sectionService: SectionService,
    private moduleService: ModuleService
  ) { }


  getAllACompose(next) {
    let returnData = function (data) {
      next(data);
    };
    let aComposeTab = new Array<ACompose>();
    let aCompose = new ACompose();
    var url = 'http://localhost:5000/app/a-compose';
    this.requestService.getRequestByUrl(url, (data) => {
      let i = 0;
      for (let line of data) {
        console.log(line.code_UF);
        this.constructAcompose(line.code_UF, line.id_section, (compose) => {
          if (line.code_UF == compose.module.code_UF) {
            console.log("code " + compose.module.code_UF);
            //aComposeTab[i] = new ACompose();
            aComposeTab.push(compose);
            console.log("acompose ");
          }
          i++;
          if (i === Object.keys(data).length) {
            next(this.aComposeTab);
          }
        });
      }
    });
    
  }

  getModuleByCode(codeUF: string, next) {
    this.moduleService.getModuleByCode(codeUF, (module) => {
      next(module);
    });
  }

  getSectionById(id: string, next) {
    this.sectionService.getSectionById(id, (section) => {
      next(section);
    });
  }

  getAComposeByModule(code: string, next) {
    var url = 'http://localhost:5000/app/a-compose/module/' + code;
    this.requestService.getRequestByUrl(url, (data) => {
      //this.aCompose = data;
      next(data);
    });
  }

  getAComposeBySection(id: number, next) {
    var url = 'http://localhost:5000/app/a-compose/section/' + id;
    this.requestService.getRequestByUrl(url, (data) => {
      this.aCompose = data;
      next(data);
    });
  }

  async constructAcompose(codeUF: string, idSection: string, next) {
    let aCompose = new ACompose();
    await this.getSectionById(idSection, (section) => {
      aCompose.section = new Section();
      aCompose.section = section[0];
      this.getModuleByCode(codeUF, (module) => {
        aCompose.module = new Module();
        aCompose.module = module[0];
          this.aComposeTab.push(aCompose);
        next(aCompose);
      });
    });
  }

  // GET ALL CONSTRUCTED ACOMPOSE
  constructAllACompose(next) {
    let aComposeTab = new Array<ACompose>();
    let aCompose = new ACompose();
    var url = 'http://localhost:5000/app/a-compose';
    this.requestService.getRequestByUrl(url, async (data) => {
      let i = 0;
      for (let i = 0; i < Object.keys(data).length; i++) {
        aCompose = new ACompose();
        aCompose.section = new Section();
        aCompose.module = new Module();

        this.constructAcompose(data[i].code_UF, data[i].id_section, (acompose) => { aComposeTab.push(acompose); });
      }
      this.sortTab(aComposeTab);
      next(aComposeTab);
    });
  }

  getModulesBySection(section: Section, next) {
    let modules = new Array<Module>();
    var url = 'http://localhost:5000/app/a-compose/modules-by-section/' + section.id_section;
    this.requestService.getRequestByUrl(url, (modulesList) => {
      modules = modulesList;
      next(modules);
    });
  }

  getSectionsByModule(module: Module, next) {
    let sections = new Array<Section>();
    var url = 'http://localhost:5000/app/a-compose/sections-by-module/' + module.code_UF;
    this.requestService.getRequestByUrl(url, (sectionsResult) => {
      sections = sectionsResult;
      next(sections);
    });
  }

  async sortTab(tab: Array<ACompose>) {
    console.log("tri");
    let aComposeArray = new Array<ACompose>();
    let maxACompose = new ACompose();
    let savedACompose = new ACompose();
    console.log("taille tab "+tab);
    for (let i = 0; i < Object.keys(tab).length; i++) {
      console.log("tri");
      maxACompose = tab[i];
      for (let j = 1; j < Object.keys(tab).length; j++) {
        if (parseInt(tab[j].module.code_UF) <= parseInt(maxACompose.module.code_UF)) {
          
          savedACompose = maxACompose;
          maxACompose = tab[j];
          tab[j] = savedACompose;
        }
      }
      tab[i] = maxACompose;
      aComposeArray.push(maxACompose);
    }
    return aComposeArray;
  }

  createACompose(aCompose: ACompose,  next) {
    this.request = "INSERT INTO a_compose VALUES('" +
      aCompose.module.code_UF + "', " + aCompose.section.id_section + ");";
    var url = "http://localhost:5000/app/a-compose/create";
    this.requestService.postRequest(url, this.request, next);
  }



  updateACompose(aCompose: ACompose, next) {
    this.request = "UPDATE a_compose SET" +
      " id_section=" + aCompose.section.id_section +
      " code_UF=" + aCompose.module.code_UF +
      " WHERE id_section=" + this.savedACompose.section.id_section +
      " AND code_UF=" + this.savedACompose.module.code_UF + ";";
  }

  async updateOrCreateACompose(aCompose: ACompose, next) {
    if (this.saved == true) {
      if (this.savedACompose.section.id_section != aCompose.section.id_section || this.savedACompose.module.code_UF != aCompose.module.code_UF) {
        this.updateACompose(aCompose, () => {
        });
      }
    }
    else {
      this.createACompose(aCompose, (response) => {
        next(response);
      });
    }
    /*this.getAComposeBySection(aCompose.section.id_section, (gottedACompose) => {
      // Si la section existe dans acompose
      if (Object.keys(gottedACompose).length != 0) {
        console.log("Section existe dans acompose...");
        if (gottedACompose.id_section == aCompose.section.id_section) {
          this.updateACompose(aCompose, () => {

          });
        }
        else {
          this.createACompose(aCompose, (response) => {
            console.log("acompose créé " + response);
          });
        }
      }
      else {
        this.createACompose(aCompose, (response) => {
          console.log("acompose créé " + response);
        });
      }
    });*/
  }

  deleteACompose(aCompose, next) {
    this.request = "DELETE FROM a_compose WHERE" +
      " id_section=" + aCompose.section.id_section +
      " AND code_UF='" + aCompose.module.code_UF + "';";
    var url = "http://localhost:5000/app/a-compose/delete";
    this.requestService.postRequest(url, this.request, (data) => {
      next(data);
    });
  }
}



