import { Component } from '@angular/core';
import { TypeDocService } from '../../../../services/type.doc.service';
import { TypeDoc } from '../../../../models/type.doc';

@Component({
  selector: 'type-doc',
  templateUrl: './type.doc.html'
})

export class DonneesTypeDocComponent {
  typesDoc: TypeDoc[];
  typeDoc = new TypeDoc();

  constructor(private typeDocService: TypeDocService) { this.afficherTypesDoc(); }

  createTypeDoc() {
    this.typeDocService.createTypeDoc(this.typeDoc, () => { this.afficherTypesDoc(); });
  }

  afficherTypesDoc() {
    this.typeDocService.getAllTypeDoc((data) => {
      console.log(data);
      this.typesDoc = data;
    });
  }

  onSubmit() {
    this.createTypeDoc();
  }

  modifier(index: number) {
    this.typeDoc = this.typesDoc[index];
  }

  update() {
    this.typeDocService.updateTypeDoc(this.typeDoc, () => { this.afficherTypesDoc(); })
  }

  delete(index: number) {
    this.typeDocService.deleteTypeDoc(this.typesDoc[index], () => { this.afficherTypesDoc(); })
  }
}



