import { Component, OnInit } from '@angular/core';
import { Etudiant } from '../etudiant';
import { EtudiantService } from '../../services/etudiant.service';
import { Ville } from '../ville';
import { Module } from '../module';
import { ACompose } from '../../models/aCompose';
import { Section } from '../../models/section';
import { ModuleService } from '../../services/module.service';
import { AComposeService } from '../../services/a.compose.service';
import { SectionService } from '../../services/section.service';
import { AContient } from '../../models/aContient';
import { AContientService } from '../../services/a.contient.service';

import { Globals } from 'globals';
import { Matiere } from '../matiere';

@Component({
  selector: 'app-module-form',
  templateUrl: './module-form.component.html',
  styleUrls: ['../../styles/form-style.css']
})
export class ModuleFormComponent {

  module = new Module();
  modules: Module[];
  submitted = false;
  aCompose = new ACompose();
  //aComposeTab = new Array<ACompose>();
  aComposeTab: ACompose[];
  sections: Section[];
  msg: string;

  constructor(
    private moduleService: ModuleService,
    private sectionService: SectionService,
    private aComposeService: AComposeService,
    private aContientService: AContientService
  ) {

    this.aCompose.module = new Module();
    this.aCompose.section = new Section();
    this.sectionService.getAllSections((data) => {
      this.sections = data;
    });
    if (this.aComposeService.saved == true) {
      this.aCompose = this.aComposeService.savedACompose;
    }
    this.afficherModules();
  }

  afficherModules() {
    this.msg = "";
    this.aComposeTab = new Array<ACompose>();
     this.aComposeService.constructAllACompose((data) => {
       this.aComposeTab = data;
    });
  }

  async onSubmit(next) {
    this.submitted = true;
    // si le module est déjà sauvé, on le modifie
    if (this.aComposeService.saved) {
      await this.moduleService.updateModule(this.aCompose.module, async () => {
        //this.aCompose.module = this.module;
        await this.aComposeService.updateOrCreateACompose(this.aCompose, () => { });
        this.msg = "Module mis à jour";
        //this.afficherModules();
      });
    }
    else {
      // Si le module n'est pas sauvé il faut le créer
      console.log("" + this.aCompose.section.id_section);
      if (typeof this.aCompose.section.id_section == 'undefined') {
        this.aCompose.section = new Section();
        this.aCompose.section.id_section = 3;
      }
      if (this.aComposeService.saved == false) {
        if (this.aCompose.module.code_UF == "") {
          this.msg = "Veuillez indiquer un numéro d'UF";
          console.log(this.msg);
        }
        else {
          await this.moduleService.createModule(this.aCompose.module, (rep) => {
              //this.aCompose.module = this.module;
            this.aComposeService.updateOrCreateACompose(this.aCompose, (rep) => {
              if (rep) {
                this.afficherModules();
              }
              });
          });
        }
        //this.aComposeTab.push(this.aCompose);
      }
      let aContient = new AContient();
      aContient.matiere = new Matiere();
      aContient.module = new Module();
      aContient.matiere.id_matiere = 11;
      aContient.module.code_UF = this.module.code_UF;
      this.aContientService.createAContient(aContient, (rep) => {
        //this.afficherModules();
      });
    }
  }

  onChange(id: any, value: any) {}

  rechercher() {
  }

  supprimer(i: number) {
    this.aComposeService.deleteACompose(this.aComposeTab[i], (rep1) => {
      if (Object.keys(rep1).length > 0) {
        this.moduleService.deleteModule(this.aComposeTab[i].module, () => {
          this.afficherModules();
        });
      }
    });
  }

  modifier(index: number) {
    this.aComposeService.savedACompose = this.aComposeTab[index];
    this.aCompose = this.aComposeService.savedACompose;
    this.aComposeService.saved = true;
  }

  reinitialiser() {
    this.aCompose = new ACompose();
    this.aComposeService.saved = false;
    this.msg = "";
  }
}



