import { BrowserModule } from '@angular/platform-browser';
import { NgModule } from '@angular/core';
import { FormsModule } from '@angular/forms';
import { HttpClientModule } from '@angular/common/http';
import { RouterModule } from '@angular/router';
import { DateValueAccessorModule } from 'angular-date-value-accessor';
import { Globals } from 'globals';
//import * as html2canvas from './components/rapports/jspdf/html2canvas';

// VALIDATORS
import { ForbiddenValidatorDirective } from './validation/forbidden-name.directive';

import { AppComponent } from './app.component';
import { NavMenuComponent } from './components/nav-menu/nav-menu.component';
import { HomeComponent } from './components/home/home.component';
import { CounterComponent } from './components/counter/counter.component';
import { FetchDataComponent } from './components/fetch-data/fetch-data.component';
import { TestComponent } from './components/test-page/test-component';
// MODULES COMPONENTS
import { ModulesComponent } from './components/modules/modules.component';
import { NavbarModulesComponent } from './components/modules/navbar/navbar.modules.component';
import { CreerModulesComponent } from './components/modules/nested/creer/creer.modules.component';
import { GestionMatieresComponent } from './components/modules/nested/matieres/gestion.matieres.component';
import { RechercherModulesComponent } from './components/modules/nested/rechercher/rechercher.modules.component';
import { GestionSectionsComponent } from './components/modules/nested/sections/gestion.sections.component';
// HORAIRES COMPONENTS
import { NavbarCoursComponent } from './components/cours/navbar/navbar.cours.component';
import { CoursCreerComponent } from './components/cours/nested/creer/cours.creer.component';
import { CoursModifierComponent } from './components/cours/nested/modifier/cours.modifier.component';
import { CoursRechercherComponent } from './components/cours/nested/rechercher/cours.rechercher.component';

// DONNEES COMPONENTS
import { DonneesComponent } from './components/donnees/donnees.component';
import { NavbarDonneesComponent } from './components/donnees/navbar/navbar.donnees.component';
import { DonneesHorairesComponent } from './components/donnees/nested/horaire/horaire.component';
import { DonneesLocalComponent } from './components/donnees/nested/local/local.component';
import { DonneesPaysComponent } from './components/donnees/nested/pays/pays.component';
import { DonneesVilleComponent } from './components/donnees/nested/ville/ville.component';
import { DonneesTypeDocComponent } from './components/donnees/nested/type-doc/type.doc.component';

// RAPPORTS COMPONENTS
import { NavbarRapportsComponent } from './components/rapports/navbar/navbar.rapports.component';
import { RapportsComponent } from './components/rapports/rapports.component';
import { ListesComponent } from './components/rapports/nested/listes/listes.component';
import { PvComponent } from './components/rapports/nested/pv/pv.component';

// SERVICES
import { AuthService } from './services/auth.service';
import { MessageService } from './services/message.service';
import { RequestService } from './services/request.service';
import { PaysService } from './services/pays.service';
import { VilleService } from './services/ville.service';
import { EtudiantService } from './services/etudiant.service';
import { ParamsService } from './services/params.service';
import { HoraireService } from './services/horaire.service';
import { TypeDocService } from './services/type.doc.service';
import { LoginService } from './services/login.service';
import { JourSemaineService } from './services/jour.semaine.service';
import { MatiereService } from './services/matiere.service';
import { ModuleService } from './services/module.service'; 
import { ProfesseurService } from './services/professeur.service';
import { CoursService } from './services/cours.service';
import { DateService } from './services/date.service';
import { SectionService } from './services/section.service';
import { AComposeService } from './services/a.compose.service';
import { AConcerneService } from './services/a.concerne.service';
import { AContientService } from './services/a.contient.service';
import { AEnseigneService } from './services/a.enseigne.service';
import { InscriptionService } from './services/inscription.service';
import { ModalService } from './services/modal.service';
import { DocumentService } from './services/document.service';
// COMPONENTS
import { LoginComponent } from './components/log-user/login.component';
import { InscriptionsComponent } from './components/inscriptions/inscriptions.component';
import { CoursComponent } from './components/cours/cours.component';
import { EncoderComponent } from './components/inscriptions/nested/encoder/encoder.component';
import { RechercherComponent } from './components/inscriptions/nested/rechercher/rechercher.component';
import { NavbarComponent } from './components/inscriptions/navbar/navbar.component';
import { InscrireComponent } from './components/inscriptions/nested/inscrire/inscrire.component';
import { ProfesseursEncoderComponent } from './components/professeurs/nested/encoder/professeurs.encoder.component';
import { ProfesseursRechercherComponent } from './components/professeurs/nested/rechercher/professeurs.rechercher.component';
import { ProfesseursAttributionsComponent } from './components/professeurs/nested/attributions/professeurs.attributions.component';
import { NavbarProfesseurs } from './components/professeurs/navbar/navbar.component';
import { ProfesseursComponent } from './components/professeurs/professeurs.component';
import { EtuInscriptionsComponent } from './components/inscriptions/nested/etu-inscriptions/etu.inscriptions.component';
import { ModalComponent } from './components/modal/modal.component';
import { NavbarAdminComponent } from './components/admin/navbar/navbar.admin.component';
import { AdminComponent } from './components/admin/admin.component';
import { AdminService } from './services/admin.service';
//FORMS
import { EtudiantFormComponent } from './models/etudiant-form/etudiant-form.component';
import { PaysFormComponent } from './models/pays-form/pays-form.component';
import { DocumentFormComponent } from './models/document-form/document-form.component';
import { ModuleFormComponent } from './models/module-form/module-form.component';
import { LocalService } from './services/local.service';
import { VilleFormComponent } from './models/ville-form/ville-form.component';
import { RapportService } from './services/rapport.service';
import { GestionUserComponent } from './components/admin/nested/gestion.user.component';
import { UserService } from './services/user.service';
import { RoleService } from './services/role.service';
import { CalendarService } from './services/calendar.service';
import { EtatsInscriptionsComponent } from './components/inscriptions/nested/Etats-inscriptions/etats.inscriptions.component';

@NgModule({
  declarations: [
    AppComponent,
    NavMenuComponent,
    HomeComponent,
    CounterComponent,
    FetchDataComponent,
    TestComponent,
    LoginComponent,
    InscriptionsComponent,
    NavbarComponent,
    EncoderComponent,
    RechercherComponent,
    ModulesComponent,
    CoursComponent,
    InscrireComponent,
    NavbarProfesseurs,
    ModalComponent,
    EtatsInscriptionsComponent,
    // FORMS
    EtudiantFormComponent,
    PaysFormComponent,
    DocumentFormComponent,
    ModuleFormComponent,
    VilleFormComponent,

    NavbarModulesComponent,
    CreerModulesComponent,
    RechercherModulesComponent,
    GestionMatieresComponent,
    NavbarCoursComponent,
    CoursCreerComponent,
    CoursRechercherComponent,
    CoursModifierComponent,
    ProfesseursEncoderComponent,
    ProfesseursRechercherComponent,
    ProfesseursAttributionsComponent,
    ProfesseursComponent,
    GestionSectionsComponent,
    EtuInscriptionsComponent,

    NavbarDonneesComponent,
    DonneesComponent,
    DonneesHorairesComponent,
    DonneesLocalComponent,
    DonneesPaysComponent,
    DonneesVilleComponent,
    DonneesTypeDocComponent,
    NavbarAdminComponent,
    AdminComponent,
    GestionUserComponent,

    // Rapports
    NavbarRapportsComponent,
    RapportsComponent,
    ListesComponent,
    PvComponent,

    // Validator
    ForbiddenValidatorDirective
  ],
  imports: [
    BrowserModule.withServerTransition({ appId: 'ng-cli-universal' }),
    HttpClientModule,
    FormsModule,
    DateValueAccessorModule,
    RouterModule.forRoot([
      { path: '', component: HomeComponent, pathMatch: 'full' },
      { path: 'login', component: LoginComponent },
      { path: 'counter', component: CounterComponent },
      { path: 'fetch-data', component: FetchDataComponent },
      { path: 'test', component: TestComponent},
      { path: 'inscriptions', component: InscriptionsComponent,
        children: [
        { path: '', redirectTo: 'encoder', pathMatch: 'full' },
        { path: 'encoder', component: EncoderComponent },
        { path: 'rechercher', component: RechercherComponent },
        { path: 'inscrire', component: InscrireComponent }, 
        { path: 'etu-inscriptions', component: EtuInscriptionsComponent },
        { path: 'etats-inscriptions', component: EtatsInscriptionsComponent }
        ]
      },
      {
        path: 'modules', component: ModulesComponent,
        children: [
          { path: '', redirectTo: 'ajouter', pathMatch: 'full' },
          { path: 'ajouter', component: CreerModulesComponent },
          { path: 'rechercher', component: RechercherModulesComponent },
          { path: 'matieres', component: GestionMatieresComponent },
          { path: 'sections', component: GestionSectionsComponent }
        ]
      },
      {
        path: 'cours', component: CoursComponent,
        children: [
          { path: '', redirectTo: 'creer', pathMatch: 'full' },
          { path: 'creer', component: CoursCreerComponent },
          { path: 'rechercher', component: CoursRechercherComponent },
          { path: 'modifier', component: CoursModifierComponent }
        ]
      },
      {
        path: 'donnees', component: DonneesComponent,
        children: [
          { path: '', redirectTo: 'horaire', pathMatch: 'full' },
          { path: 'horaire', component: DonneesHorairesComponent },
          { path: 'locaux', component: DonneesLocalComponent },
          { path: 'ville', component: DonneesVilleComponent },
          { path: 'pays', component: DonneesPaysComponent },
          { path: 'types-doc', component: DonneesTypeDocComponent }
        ]
      },
      {
        path: 'rapports', component: RapportsComponent,
        children: [
          { path: '', redirectTo: 'listes', pathMatch: 'full' },
          { path: 'listes', component: ListesComponent },
          { path: 'pv', component: PvComponent },
        ]
      },
      {
        path: 'enseignants', component: ProfesseursComponent,
        children: [
          { path: '', redirectTo: 'encoder', pathMatch: 'full' },
          { path: 'encoder', component: ProfesseursEncoderComponent },
          { path: 'rechercher', component: ProfesseursRechercherComponent },
          { path: 'attributions', component: ProfesseursAttributionsComponent }
        ]
      },
      {
        path: 'admin', component: AdminComponent,
        children: [
          { path: '', redirectTo: 'gestion', pathMatch: 'full' },
          { path: 'gestion', component: GestionUserComponent }
        ]
      }
      //{ path: 'test', component: TestComponent, canActivate: [AuthService] },
      //{ path: 'inscriptions', component: InscriptionsComponent, canActivate: [AuthService]  },
    ])
  ],
  providers: [
    AuthService,
    LoginService,
    RequestService,
    PaysService,
    VilleService,
    EtudiantService,
    ParamsService,
    CoursService,
    ModuleService,
    HoraireService,
    LocalService,
    TypeDocService,
    JourSemaineService,
    MatiereService,
    ProfesseurService,
    DateService,
    SectionService,
    AComposeService,
    AEnseigneService,
    AConcerneService,
    AContientService,
    InscriptionService,
    RapportService,
    ModalService,
    DocumentService,
    AdminService,
    UserService,
    RoleService,
    CalendarService,
  ],
  bootstrap: [AppComponent]
})
export class AppModule { }



