import { Component } from '@angular/core';

@Component({
  selector: 'navbar-admin',
  templateUrl: './navbar.admin.html',
  styleUrls: ['./style.css']
})
export class NavbarAdminComponent {
  isExpanded = false;
  collapse() {
    this.isExpanded = false;
  }

  toggle() {
    this.isExpanded = !this.isExpanded;
  }
}



