import { Etudiant } from './etudiant';
import { TypeDoc } from './type.doc';
export class Document {

  id_document: number;
  valide: boolean;
  date_remise: string;
  date_deb_validite: string;
  date_fin_validite: string;
  langue: string;
  a_payer: number;
  montant_paye: number;
  paye: boolean;
  signature: boolean;
  etudiant: Etudiant;
  type_doc: TypeDoc;

  constructor() {
    this.valide = false;
    this.date_remise = "";
    this.date_deb_validite = "";
    this.date_fin_validite = "";
    this.langue = "";
    this.a_payer = 0;
    this.montant_paye = 0;
    this.paye = false;
    this.signature = false;
    this.etudiant = new Etudiant();
    this.type_doc = new TypeDoc();
  }
}



