import { Horaire } from './horaire';
import { Local } from './local';
import { Module } from './module';
import { Matiere } from './matiere';
import { JourSemaine } from './jourSemaine';
import { Professeur } from './professeur';

export class Cours {
  public id_cours: number;
  //public date_deb: Date;
  //public date_fin: Date;
  public date_deb: string;
  public date_fin: string;
  public horaire: Horaire;
  public local: Local;
  public matiere: Matiere;
  public professeur: Professeur;
  public module: Module;
  public jourSemaine: JourSemaine;

  constructor() {
    this.date_deb = "";
    this.date_fin = "";
    this.horaire = new Horaire();
    this.local = new Local();
    this.matiere = new Matiere();
    this.professeur = new Professeur();
    this.module = new Module();
    this.jourSemaine = new JourSemaine();
  }
}



