import { Component } from '@angular/core';

@Component({
  selector: 'navbar-donnees',
  templateUrl: './navbar.donnees.html',
  styleUrls: ['./style.css']
})
export class NavbarDonneesComponent {
  isExpanded = false;
  collapse() {
    this.isExpanded = false;
  }

  toggle() {
    this.isExpanded = !this.isExpanded;
  }
}



