import { Component } from '@angular/core';

@Component({
  selector: 'navbar-cours',
  templateUrl: './navbar.cours.html',
  styleUrls: ['./style.css']
})
export class NavbarCoursComponent {
  isExpanded = false;
  collapse() {
    this.isExpanded = false;
  }

  toggle() {
    this.isExpanded = !this.isExpanded;
  }
}



