import { Component } from '@angular/core';
import { EtudiantService } from '../../../../services/etudiant.service';
import { Inscription } from '../../../../models/inscription';
//import { Etudiant } from '../../../../models/etudiant';
import { Section } from '../../../../models/section';
import { ACompose } from '../../../../models/aCompose';
import { Module } from '../../../../models/module';
import { SectionService } from '../../../../services/section.service';
import { AComposeService } from '../../../../services/a.compose.service';
import { ModuleService } from '../../../../services/module.service';
import { Cours } from '../../../../models/cours';
import { CoursService } from '../../../../services/cours.service';
import { InscriptionService } from '../../../../services/inscription.service';

@Component({
  selector: 'etu-inscriptions',
  templateUrl: './etu.inscriptions.html',
  styleUrls: ['../../../../styles/form-style.css']
})
export class EtuInscriptionsComponent {
  inscription = new Inscription();
  modules = new Array<Module>();
  sections = new Array<Section>();
  aCompose = new Array<ACompose>();
  coursTab = new Array<Cours>();
  inscriptions = new Array<Inscription>();
  formationsChoisies = new Array<Cours>();
  indexTab = new Array<boolean>(); // indique les formations choisies
  dateActu: string;
  msg: string;

  constructor(
    private etudiantService: EtudiantService,
    private moduleService: ModuleService,
    private coursService: CoursService,
    private sectionService: SectionService,
    private aComposeService: AComposeService,
    private inscriptionService: InscriptionService
  ) {
    //this.inscription = new Inscription();
    this.afficherCours();
  }

  afficherCours() {
    if (!this.inscription.date_inscription) { this.inscription.date_inscription = this.inscriptionService.dateActuFr(); }
    //this.dateActu = date.toLocaleDateString();
    if (this.etudiantService.createdEtu) {
      this.inscription.etudiant = this.etudiantService.getActualEtudiant();
      // récupère les formations choisies
      this.inscriptionService.formationChoisiesByEtuTrieesParCode(this.etudiantService.savedEtudiant.id_etudiant, (tabIndex, coursTab, coursEtu) => {
        this.coursTab = coursTab;
        this.formationsChoisies = coursEtu;
        //console.log("formations choisies etu " + this.formationsChoisies[0].module.intitule);
      });
    }
    else {
      this.msg = "Aucun étudiant sélectionné, veuillez d'abord sélectionner un étudiant";
      this.inscriptionService.msg = this.msg;
    }
    this.sectionService.getAllSections((sections) => {
      this.sections = sections;
    });
    this.moduleService.getAllModules((modules) => {
      this.modules = modules;
    });
    this.coursService.getAllCours((allCours) => {
      this.coursTab = allCours;
    });
  }

  creer(i: number) {
    //this.inscription.cours = new Cours();
    let inscrit = false;
    console.log("id cours choisi " + i);
    //console.log("cours choisi " + this.formationsChoisies[i].id_cours);
    for (let j = 0; j < this.formationsChoisies.length; j++) {
      if (this.formationsChoisies[j].id_cours == i) {
        console.log("cours choisi " + this.formationsChoisies[j].id_cours);
        inscrit = true;
        this.msg = "L'étudiant est déjà inscrit à ce cours";
      }
    }
    if (inscrit == false) {
      console.log("value cours " + i);
      this.inscription.cours.id_cours = i;
      //this.inscription.etudiant = new Etudiant();
      //this.inscription.etudiant.id_etudiant = this.etudiantService.savedEtudiant.id_etudiant;
      this.inscriptionService.createInscription(this.inscription, async (response) => {
        await this.inscriptionService.getInscriptionById(response.insertId, (inscription) => {
          this.msg = "Inscription créée";
          //this.inscription = inscription;
          this.afficherCours();
        });
      });
    }
  }

  updateInscription() {
    this.inscriptionService.updateInscription(this.inscription, () => {
    });
    this.afficherCours();
    this.msg = "Inscription mise à jour";
  }

  modifier(index: number) {
    this.inscriptionService.getInscriptionByCoursAndEtudiantId(this.formationsChoisies[index].id_cours, this.etudiantService.savedEtudiant.id_etudiant, (inscription) => {
      this.inscription = inscription;
      this.inscription.etudiant = this.etudiantService.savedEtudiant;
      this.inscription.cours = this.formationsChoisies[index];
    });
  }

  nouvelleInscription() {
    this.inscription = new Inscription();
    if (!this.inscription.date_inscription) { this.inscription.date_inscription = this.inscriptionService.dateActuFr(); }
    this.inscription.etudiant = this.etudiantService.savedEtudiant;
  }

  checkIf(index: number) {
    console.log("index " + index);
    console.log("indexTab " + this.indexTab[index]);
    return this.indexTab[index];
  }

  async supprimer(i: number) {
    await this.inscriptionService.deleteInscription(this.formationsChoisies[i].id_cours, this.etudiantService.savedEtudiant.id_etudiant, (rep) => {
      if (rep != null) {
        //this.constructor();
      }
    });
    this.msg = "Inscription supprimée!";
    this.afficherCours();
  }
}



