import { Component, OnDestroy } from '@angular/core';
import { LoginService } from '../../services/login.service';
import { Observable } from 'rxjs/Observable';
import { Subscription } from 'rxjs/Subscription';

@Component({
  selector: 'app-home',
  templateUrl: './home.component.html',
  styleUrls: ['../../styles/form-style.css']
})

export class HomeComponent implements OnDestroy {
  msg: any;
  logged: boolean = false;
  subscription: Subscription;
  constructor(private loginService: LoginService) {
    if (this.loginService.logged == false) { this.msg = "Veuillez vous connecter...";}
    this.subscription = this.loginService.getMessage().subscribe(message => { this.msg = message.text; this.logged = message.logged;});
  }

  changeMsg(message) {
    console.log(this.logged);
    this.logged = message.logged;
    this.msg = message.text;
  }

  ngOnDestroy() {
    // unsubscribe to ensure no memory leaks
    this.subscription.unsubscribe();
  }
}



