import { Component, Input, OnInit, OnDestroy } from '@angular/core';
import { Router, RouterModule, ActivatedRoute } from '@angular/router';
//import { MysqlConnectService } from '../services/mysql.connect.service';
import { HttpClient } from '@angular/common/http';
import { EtudiantService } from '../../../../services/etudiant.service';
import { Professeur } from '../../../../models/professeur';
import { ProfesseurService } from '../../../../services/professeur.service';

@Component({
  selector: 'professeurs-rechercher-component',
  templateUrl: './professeurs.rechercher.html'
})
export class ProfesseursRechercherComponent{

  public professeurs: Professeur[];
 
  @Input() id: string;
  @Input() nom: string;
  @Input() prenom: string;
  empty: number;
  public message: string;

  constructor(private router: Router, private professeurService: ProfesseurService) { this.afficherProfs(); }

  afficherProfs() {
    this.professeurService.getAllProfesseurs((response) => {
      this.professeurs = response;
    });
  }
}



