import { Component } from '@angular/core';
import { Matiere } from '../../../../models/matiere';
import { Module } from '../../../../models/module';
import { AContient } from '../../../../models/aContient';
import { AContientService } from '../../../..//services/a.contient.service';
import { MatiereService } from '../../../../services/matiere.service';
import { ModuleService } from '../../../../services/module.service';

@Component({
  selector: 'gestion-matieres',
  templateUrl: './gestion.matieres.html',
  styleUrls: ['../../../../styles/form-style.css']
})

export class GestionMatieresComponent {

  matiere = new Matiere();
  aContient = new AContient();
  oldAContient = new AContient();
  module: Module;
  matieres: Matiere[];
  modules = new Array<Module>();
  aContientTab = new Array<AContient>();
  aContientTabMatieresModule = new Array<AContient>();
  msg: string;

  constructor(
    private moduleService: ModuleService,
    private matiereService: MatiereService,
    private aContientService: AContientService) {

    this.aContient.module = new Module();
    this.aContient.matiere = new Matiere();
    this.moduleService.getAllModules((data) => {
      this.modules = data;
    });

    if (this.aContientService.saved == true) {
      this.aContient = this.aContientService.savedAContient;
    }
    this.afficherMatieres();
  }

  async afficherMatieres() {
    await this.aContientService.constructAllAContient((data) => {
      this.aContientTab = data;
    });
  }

  onSubmit() {
    if (typeof this.aContient.module.code_UF == 'undefined') {
      this.aContient.module = new Module();
      this.aContient.module.code_UF = "0";
    }
    if (this.aContientService.saved == false) {
      if (this.aContient.matiere.intitule == "") {
        this.msg = "Veuillez indiquer un nom de matière";
        console.log(this.msg);
      }
      else {
        this.matiereService.createMatiere(this.aContient.matiere.intitule, this.aContient.matiere.nb_periodes, (response) => {
          //this.aContient.module = this.module;
          this.aContient.matiere.id_matiere = response.insertId;
          this.aContientService.updateOrCreateAContient(this.aContient, () => {
            this.afficherMatieres();
            this.msg = "Matière créée";
          });
        });
      }
    }
    // Si le module existe, on le met à jour
    else {
      this.matiereService.updateMatiere(this.aContient.matiere, () => {
        //this.aCompose.module = this.module;
        this.msg = "Matière modifiée";
        //this.aContientService.oldModule = this.module;
        this.aContientService.updateAContient(this.aContient, this.oldAContient, () => {
          this.afficherMatieres();
        });
      });
    }
  }

  reinitialiser() {
    this.aContient = new AContient();
    this.aContient.matiere = new Matiere();
    this.aContient.module = new Module();
    this.aContientService.saved = false;
  }

  modifier(index: number) {
    this.aContientService.savedAContient = this.aContientTab[index];
    //this.module = this.aContientTab[index].module;
    console.log("module " + this.aContientService.savedAContient.module.code_UF);
    this.aContient = this.aContientTab[index];
    this.oldAContient.module = new Module();
    this.oldAContient.matiere = new Matiere();
    this.oldAContient.module.code_UF = this.aContientTab[index].module.code_UF;
    this.oldAContient.matiere.id_matiere = this.aContientTab[index].matiere.id_matiere;
    this.aContientService.saved = true;
  }

  supprimer(i: number) {
    this.aContientService.deleteAContient(this.aContientTab[i], () => {
      this.matiereService.deleteMatiere(this.aContientTab[i].matiere, () => { this.afficherMatieres(); this.msg = "Matière supprimée"; });
    });
  }

  onChange(args: string) {
    console.log(args.substring(args.length - 3, args.length));
    let code = args.substring(args.lastIndexOf(" ") + 1, args.length);
    this.aContientTabMatieresModule = new Array<AContient>();
    let aContientTmp = new Array<AContient>();
    for (let i = 0; i < this.aContientTab.length; i++) {
      if (this.aContientTab[i].module.code_UF == code) {
        aContientTmp.push(this.aContientTab[i]);
      }
    }
    let i = 0;

    this.aContientTabMatieresModule = aContientTmp;
  }
}



