import { Component } from '@angular/core';
import { EtudiantService } from '../../../../services/etudiant.service';
import { Inscription } from '../../../../models/inscription';
//import { Etudiant } from '../../../../models/etudiant';
import { Section } from '../../../../models/section';
import { ACompose } from '../../../../models/aCompose';
import { Module } from '../../../../models/module';
import { SectionService } from '../../../../services/section.service';
import { AComposeService } from '../../../../services/a.compose.service';
import { ModuleService } from '../../../../services/module.service';
import { Cours } from '../../../../models/cours';
import { CoursService } from '../../../../services/cours.service';
import { InscriptionService } from '../../../../services/inscription.service';

@Component({
  selector: 'inscrire',
  templateUrl: './inscrire.html',
  styleUrls: ['../../../../styles/form-style.css']
})
export class InscrireComponent {
  inscription: Inscription;
  modules = new Array<Module>();
  sections = new Array<Section>();
  aCompose = new Array<ACompose>();
  coursTab = new Array<Cours>();
  inscriptions = new Array<Inscription>();
  inscriptionsSaved = new Array<Inscription>();
  formationsChoisies = new Array<Cours>();
  formationsSaved = new Array<Cours>();
  //formationsSuppression = new Array<Cours>();
  dateActu: string;
  indexTab = new Array<boolean>(); // indique les formations choisies
  msg: string;

  constructor(
    private etudiantService: EtudiantService,
    private moduleService: ModuleService,
    private coursService: CoursService,
    private sectionService: SectionService,
    private aComposeService: AComposeService,
    private inscriptionService: InscriptionService
  ) {
    this.afficherCours();
  }

  afficherCours() {
    this.initialiser(0);
    this.coursService.getAllCours((allCours) => {
      console.log("inscription - cours " + allCours);
      this.coursTab = allCours;
    });
  }


  initialiser(tri:number) {
    let date = new Date();
    this.dateActu = date.toLocaleDateString();
    this.inscription = new Inscription();
    if (this.etudiantService.createdEtu) {
      this.inscription.etudiant = this.etudiantService.getActualEtudiant();
      // récupère les formations choisies à modifier
      if (tri == 1) {
        this.inscriptionService.formationChoisiesByEtuTrieesParNom(this.etudiantService.savedEtudiant.id_etudiant, (tabIndex, coursTab, coursEtu) => {
          this.indexTab = tabIndex;
          this.coursTab = coursTab;
          this.formationsChoisies = coursEtu; // on reçoit les choix
        });
        //copie safe des formations choisies
        this.inscriptionService.formationChoisiesByEtuTrieesParNom(this.etudiantService.savedEtudiant.id_etudiant, (tabIndex, coursTab, coursEtu) => {
          this.formationsSaved = coursEtu; // on sauve les choix de départ
          console.log("formations debut à supprimer " + Object.keys(this.formationsSaved).length);
        });
      }
      else {
        this.inscriptionService.formationChoisiesByEtuTrieesParCode(this.etudiantService.savedEtudiant.id_etudiant, (tabIndex, coursTab, coursEtu) => {
          this.indexTab = tabIndex;
          this.coursTab = coursTab;
          this.formationsChoisies = coursEtu; // on reçoit les choix
        });
        //copie safe des formations choisies
        this.inscriptionService.formationChoisiesByEtuTrieesParCode(this.etudiantService.savedEtudiant.id_etudiant, (tabIndex, coursTab, coursEtu) => {
          this.formationsSaved = coursEtu; // on sauve les choix de départ
          console.log("formations debut à supprimer " + Object.keys(this.formationsSaved).length);
        });
      }
    }
    else {
      this.msg = "Aucun étudiant sélectionné, veuillez d'abord sélectionner un étudiant";
      this.inscriptionService.msg = this.msg;
    }
    this.sectionService.getAllSections((sections) => {
      this.sections = sections;
    });
  }

  validerChoix() {
    let inscription: Inscription;
    var date = new Date();
    for (let i = 0; i < Object.keys(this.formationsSaved).length; i++) {
      for (let j = 0; j < Object.keys(this.formationsChoisies).length; j++) {
        if (this.formationsSaved[i] == this.formationsChoisies[j]) {
          this.formationsSaved.splice(i, 1);
          this.formationsChoisies.splice(j, 1);
        }
      }
    }
    // suppression des formations en trop
    if (Object.keys(this.formationsSaved).length > 0) {
      for (let i = 0; i < Object.keys(this.formationsSaved).length; i++) {
        this.inscriptionService.deleteInscription(this.formationsSaved[i].id_cours, this.etudiantService.savedEtudiant.id_etudiant, () => { });
      }
    }
    // ajout des formations choisies
    if (Object.keys(this.formationsChoisies).length > 0) {
      for (let cours of this.formationsChoisies) {
        inscription = new Inscription();
        inscription.date_inscription = date.toString();
        inscription.notes_sup = "";
        inscription.etudiant.id_etudiant = this.etudiantService.savedEtudiant.id_etudiant;
        inscription.cours = cours;
        this.inscriptions.push(inscription);
      }
      this.inscriptionService.createAllInscriptions(this.inscriptions, () => {
      });
    }
    this.msg = "Choix mis à jour";
  }

  addOrRemoveSelection(index: number) {
    let date = new Date();
    console.log(date.toISOString());
    console.log("index " + index);
    // Si le cours est décochée
    if (this.indexTab[index] == true) {
      this.indexTab[index] = false;
      // on regarde toutes les formations choisies
      for (let i = 0; i < this.formationsChoisies.length; i++) {
        // lorsque la formation est trouvée
        if (this.formationsChoisies[i].id_cours == this.coursTab[index].id_cours) {
          // supprimer la formation décochée
          this.formationsChoisies.splice(i, 1);
        }
      }
    }
    else {
      this.indexTab[index] = true;
      this.formationsChoisies.push(this.coursTab[index]);
      console.log("choisi " + this.coursTab[index].module.intitule);
    }
  }

  checkIf(index: number) {
    console.log("index " + index);
    console.log("indexTab " + this.indexTab[index]);
    return this.indexTab[index];
  }
}



