import { Injectable } from '@angular/core';
import { RequestService } from './request.service';
import { HttpClient } from '@angular/common/http';
import { Professeur } from '../models/professeur';
import { DateService } from '../services/date.service';
import { element } from 'protractor';

@Injectable()
export class ProfesseurService {

  request: string;
  savedProfesseur = new Professeur();
  saved: boolean;

  constructor(private requestService: RequestService, private dateService: DateService) { }

  getAllProfesseurs(next) {
    let professeurs = new Array<Professeur>();
    var url = 'http://localhost:5000/app/professeur/';
    this.requestService.getRequestByUrl(url, (data) => {
      for (let prof of data) {
        let converted = this.dateService.convertSQLDateToString(prof.date_naiss);
        prof.date_naiss = converted;
        professeurs.push(prof);
        console.log('converti ');
        next(professeurs);
      }
      next(data);
    });
  }

  async getProfesseurByMatricule(matriculeProf: string, next) {
    var url = 'http://localhost:5000/app/professeur/matricule/' + matriculeProf;
    await this.requestService.getRequestByUrl(url, async (data) => {
      await next(data);
    });
  }

  getProfesseurByNom(nomProf: string, next) {
    var url = 'http://localhost:5000/app/professeur/nom/' + nomProf;
    this.requestService.getRequestByUrl(url, (data) => {
      next(data);
    });
  }

  createProfesseur(professeur: Professeur, next) {
    let dateNaiss = this.dateService.convertDateToSql(professeur.date_naiss);
    this.request = "INSERT INTO professeur VALUES('" +
      professeur.matricule + "', '" +
      professeur.nom + "', '" +
      professeur.prenom + "', " +
      '"' + this.dateService.convertDateToSql(professeur.date_naiss) + '", "' +
      professeur.diplome + '");';
    var url = 'http://localhost:5000/app/professeur/create';
    this.requestService.postRequest(url, this.request, next);
  }

  updateProfesseur(professeur: Professeur, next) {
    this.request = "UPDATE professeur SET " +
      "nom='" + professeur.nom + "', " +
      "prenom='" + professeur.prenom + "', " +
      "date_naiss='" + this.dateService.convertDateToSql(professeur.date_naiss) + "', " +
      'diplome="' + professeur.diplome + '" WHERE matricule=' + professeur.matricule + ";";
    var url = 'http://localhost:5000/app/professeur/update';
    this.requestService.postRequest(url, this.request, () => {
      next();
    });
  }

  deleteProfesseur(professeur: Professeur, next) {
    this.request = "DELETE FROM professeur WHERE matricule='" + professeur.matricule + "';";
    var url = "http://localhost:5000/app/professeur/delete";
    this.requestService.postRequest(url, this.request, (rep) => {
      if (rep.errno > 0) {
        next(null);
      } else {
        next(rep);
      }
    });
  }

}



