export class Module {

  code_UF: string;
  no_UF: string;
  niv_UF: string;
  intitule: string;
  nb_periodes: number;
  niv_enseignement: number;

  constructor() {
    this.code_UF = null;
    this.no_UF = null;
    this.niv_UF = "";
    this.intitule = "";
    this.nb_periodes = null;
    this.niv_enseignement = null;
  }
}



