import { Component, Input, OnInit, OnDestroy } from '@angular/core';
import { Router, RouterModule, ActivatedRoute } from '@angular/router';
//import { MysqlConnectService } from '../services/mysql.connect.service';
import { Etudiant } from '../../../../models/etudiant';
import { HttpClient } from '@angular/common/http';
import { EtudiantService } from '../../../../services/etudiant.service';
import { ModuleService } from '../../../../services/module.service';
import { CoursService } from '../../../../services/cours.service';
import { SectionService } from '../../../../services/section.service';
import { AComposeService } from '../../../../services/a.compose.service';
import { InscriptionService } from '../../../../services/inscription.service';
import { Inscription } from '../../../../models/inscription';
import { Module } from '../../../../models/module';
import { Section } from '../../../../models/section';
import { ACompose } from '../../../../models/aCompose';
import { Cours } from '../../../../models/cours';
import { Liste } from '../../../../models/liste';
import { RapportService } from '../../../../services/rapport.service';

@Component({
  selector: 'inscriptions-rechercher-component',
  templateUrl: './rechercher.html'
})
export class RechercherComponent{

  public etudiants: Etudiant[];
  //etudiant: Etudiant;
  @Input() id: string;
  @Input() nom: string;
  @Input() prenom: string;
  empty: number;
  indexEtu: number;
  confirm: boolean;
  public message: string;
  savedClass = new Array<Etudiant>();

  inscription = new Inscription();
  modules = new Array<Module>();
  sections = new Array<Section>();
  aCompose = new Array<ACompose>();
  coursTab = new Array<Cours>();
  inscriptions = new Array<Inscription>();
  savedEtudiants = new Array<Etudiant>();
  formationsChoisies = new Array<Cours>();
  liste = new Array<Liste>();
  indexTab = new Array<boolean>(); // indique les formations choisies


  constructor(
    private router: Router,
    private http: HttpClient,
    private etudiantService: EtudiantService,
    private moduleService: ModuleService,
    private coursService: CoursService,
    private sectionService: SectionService,
    private aComposeService: AComposeService,
    private inscriptionService: InscriptionService,
    private rapportService: RapportService
  ) {
    this.confirm = false;
    this.afficherEtu();
    this.coursService.getAllCours((allCours) => {
      this.coursTab = allCours;
    });
  }

  public afficherEtu() {
    this.message = "";
    if (this.savedClass.length == 0) {
      this.reinitialiserEtu();
    }
    else {
      this.etudiants = this.savedClass;
    }
  }

  public reinitialiserEtu() {
    this.etudiantService.getAllEtudiants((data) => {
      this.savedEtudiants = data;
      this.etudiants = data;
      this.savedClass = new Array<Etudiant>();
    });
  }

  public rechercherParId() {
    this.savedClass = new Array<Etudiant>();
    this.empty = 0;
    this.message = "";
    this.etudiantService.getEtudiantById(this.id, (result) => {
      this.etudiants = result;
      this.empty = result.length;
    });
    if (this.empty == 0)  { this.message = "Aucun étudiant trouvé avec cet identifiant..."; }
  }

  public rechercherParNom() {
    this.savedClass = new Array<Etudiant>();
    this.empty = 0;
    this.message = "";
    this.etudiantService.getEtudiantByNom(this.nom, (result) => {
      this.etudiants = result;
      this.empty = result.length;
    });
    if (this.empty == 0) { this.message = "Aucun étudiant trouvé avec ce nom..."; }
  }

  async selectedCours(idCours) {
    //this.coursTab[index].id_cours;
    console.log("test change" + idCours);
    let etus = new Array<Etudiant>();
    this.etudiants = this.savedEtudiants;
    this.rapportService.getListeEtudiantsNormalParCours(idCours, (liste) => {
      for (let i = 0; i < Object.keys(liste).length; i++) {
        for (let j = 0; j < this.etudiants.length; j++) {
          if (liste[i].nom == this.savedEtudiants[j].nom) {
            etus[i] = new Etudiant();
            etus[i] = this.savedEtudiants[j];
          }
        }
        /*if (i == Object.keys(liste).length) {
          this.etudiants = etus;
        }*/
      }
      this.etudiants = etus;
    });
  }

  public modifier(index: number) {
    console.log(this.etudiants[index]);
    this.etudiantService.normalizeAndKeepThisEtudiant(this.etudiants[index], () => {
      this.router.navigateByUrl('inscriptions/encoder');
    });
  }

  confirmSup(index: number) {
    this.confirm = true;
    this.indexEtu = index;
  }

  public supprimer() {
    this.confirm = false;
    this.etudiantService.deleteEtudiant(this.etudiants[this.indexEtu], async () => {
      this.message = "Etudiant supprimé";
      this.afficherEtu();
    });
  }

  annuler() {
    this.confirm = false;
  }

  public inscrire(index: number) {
    console.log(this.etudiants[index]);
    this.etudiantService.normalizeAndKeepThisEtudiant(this.etudiants[index], () => {
      this.router.navigateByUrl('inscriptions/inscrire');
    });
  }
}



