import { Component } from '@angular/core';

@Component({
  selector: 'navbar-modules',
  templateUrl: './navbar.modules.html',
  styleUrls: ['./style.css']
})
export class NavbarModulesComponent {
  isExpanded = false;
  collapse() {
    this.isExpanded = false;
  }

  toggle() {
    this.isExpanded = !this.isExpanded;
  }
}



