import { Component, Input, ViewChild, ElementRef, Renderer2 } from '@angular/core';
import { Router, RouterModule, ActivatedRoute } from '@angular/router';
import { AngularDateTimePickerModule } from 'angular2-datetimepicker';
import { Globals } from 'globals';
import { Cours } from '../../../../models/cours';
import { CoursService } from '../../../../services/cours.service';
import { HoraireService } from '../../../../services/horaire.service';
import { LocalService } from '../../../../services/local.service';
import { ModuleService } from '../../../../services/module.service';
import { MatiereService } from '../../../../services/matiere.service';
import { JourSemaineService } from '../../../../services/jour.semaine.service';
import { ProfesseurService } from '../../../../services/professeur.service';
import { Horaire } from '../../../../models/horaire';
import { Local } from '../../../../models/local';
import { Module } from '../../../../models/module';
import { Matiere } from '../../../../models/matiere';
import { JourSemaine } from '../../../../models/jourSemaine';
import { Professeur } from '../../../../models/professeur';
import { DateService } from '../../../../services/date.service';
import { InscriptionService } from '../../../../services/inscription.service';
import { AContientService } from '../../../../services/a.contient.service';
import { AContient } from '../../../../models/aContient';

@Component({
  selector: 'cours-creer',
  templateUrl: './cours.creer.html',
  styleUrls: ['../../../../styles/form-style.css']
})


export class CoursCreerComponent {
  cours = new Cours();
  horaires: Horaire[];
  locaux: Local[];
  modules: Module[];
  matieres: Matiere[];
  professeurs: Professeur[];
  jours: JourSemaine[];
  coursTab = new Array<Cours>();
  date: Date = new Date();
  tabInscris = new Array<Boolean>();
  tabNbInscris = new Array<number>();
  aContientTabMatieresModule = new Array<AContient>();
  settings = {
    bigBanner: true,
    timePicker: false,
    format: 'dd-MM-yyyy',
    defaultOpen: true
  }
  msg: string;

  constructor(
    private router: Router,
    private coursService: CoursService,
    private horaireService: HoraireService,
    private localService: LocalService,
    private moduleService: ModuleService,
    private matiereService: MatiereService,
    private jourService: JourSemaineService,
    private professeurService: ProfesseurService,
    private dateService: DateService,
    private inscriptionService: InscriptionService,
    private aContientService: AContientService
  ) {
    if (this.coursService.saved == true) {
      this.cours = this.coursService.savedCours;
    }
    //this.getAllComponents();
    this.afficherCours("");
  }

  getAllComponents() {
    this.horaireService.getAllHoraires((horaires) => {
      this.horaires = horaires;
      this.localService.getAllLocaux((locaux) => {
        this.locaux = locaux;
        this.moduleService.getAllModules((modules) => {
          this.modules = this.moduleService.triParCodeUF(modules);
          this.matiereService.getAllMatieres((matieres) => {
            this.matieres = matieres;
            this.jourService.getAllJours((jours) => {
              this.jours = jours;
              this.professeurService.getAllProfesseurs((professeurs) => {
                this.professeurs = professeurs;
              });
            });
          });
        });
      });
    });
  }// end of getAllComponents

  public afficherCours(msg) {
    this.msg = msg;
    this.getAllComponents();
    this.coursService.getAllCours((data) => {
      this.coursTab = data;
      this.coursService.coursTriParCodeFormation(this.coursTab);
      for (let i = 0; i < this.coursTab.length; i++) {
        this.inscriptionService.getInscriptionByCoursId(this.coursTab[i].id_cours, (data) => {
          if (Object.keys(data).length > 0) {
            this.tabInscris[i] = true;
            this.tabNbInscris[i] = Object.keys(data).length;
          }
          else {
            this.tabInscris[i] = false;
          }
        });
      }
    });
  }

  onSubmit() {
    // ENREGISTRER LE COURS
    let coursTmp = this.cours;
    let coursTmp2 = this.cours;
    coursTmp.date_deb = this.dateService.cutDateStringNotAdjusted(coursTmp2.date_deb);
    coursTmp.date_fin = this.dateService.cutDateStringNotAdjusted(coursTmp2.date_fin);
    console.log("date deb " + coursTmp.date_deb);
    console.log("date fin " + coursTmp.date_fin);

    //this.cours.date_deb = this.dateService.convertToInputString(this.cours.date_deb);
    //this.cours.date_fin = this.dateService.convertToInputString(this.cours.date_fin);
    this.coursService.testIfCoursExistAlready(coursTmp, async(msg) => {
      await this.afficherCours(msg);
      if (msg != "cours créé") {
        this.cours.date_deb = this.dateService.convertToInputString(this.dateService.cutDateStringNotAdjusted(this.cours.date_deb));
        this.cours.date_fin = this.dateService.convertToInputString(this.dateService.cutDateStringNotAdjusted(this.cours.date_fin));
        //this.cours = coursTmp;
      }
    });
    /*this.coursService.createCours(this.cours, () => {
    });*/
  }

  reinitialiser() {
    this.coursService.savedCours = new Cours();
    this.cours = new Cours();
    this.msg = "";
  }

  onChange(id: string, selectedValue: string) {
    console.log(selectedValue + ' affiché');
    //this.checkValidity();
  }

  checkValidity() {
    if (this.cours.date_deb != "" && this.cours.date_fin != "" &&
      this.cours.horaire.id_horaire != null &&
      this.cours.jourSemaine.id_jour != null &&
      this.cours.local.no_local != null)
    {
      for (let i = 0; i < this.horaires.length; i++) {
        if (this.cours.horaire.id_horaire == this.horaires[i].id_horaire) {
        }
      }
    }
  }

  modifier(index: number) {
    this.cours = this.coursTab[index];
    this.cours.date_deb = this.dateService.convertToInputString(this.cours.date_deb);
    this.cours.date_fin = this.dateService.convertToInputString(this.cours.date_fin);
  }

  update() {
    console.log(this.cours);
    let coursTmp = this.cours;
    coursTmp.date_deb = this.dateService.cutDateStringNotAdjusted(this.cours.date_deb);
    coursTmp.date_fin = this.dateService.cutDateStringNotAdjusted(this.cours.date_fin);
    this.coursService.updateCours(coursTmp, (msg) => {
      this.afficherCours(msg);
    });
  }

  supprimer(index: number) {
    console.log("Supprimer ")
    this.coursService.deleteCours(this.coursTab[index].id_cours, () => {
      this.afficherCours("cours supprimé!");
    });
  }

  // DEFINIR LA MATIERE POUR LE COURS SELECTIONNE
  onChangeMatiere(args: string) {
    console.log(args.substring(args.length - 3, args.length));
    let code = args.substring(args.lastIndexOf(" ") + 1, args.length);
    this.aContientTabMatieresModule = new Array<AContient>();
    this.matieres = new Array<Matiere>();
    this.aContientService.getAContientByModule(code, (acontientTab) => {
      for (let i = 0; i < Object.keys(acontientTab).length; i++) {
        this.matieres[i] = new Matiere();
        this.matieres[i] = acontientTab[i].matiere;
        console.log(this.matieres[i].intitule);
      }
    });
    /*for (let i = 0; i < this.matieres.length; i++) {
      if (this.aContientTab[i].module.code_UF == code) {
        aContientTmp.push(this.aContientTab[i]);
      }
    }*/
    // i = 0;

    //this.aContientTabMatieresModule = aContientTmp;
  }
}



