  async getCoursByEtudiantId(idEtu: number, next) {
    let coursTableau = new Array<Cours>();
    var url = 'http://localhost:5000/app/cours/etudiant/' + idEtu;
    await this.requestService.getRequestByUrl(url, async (coursTab) => {
      if (Object.keys(coursTab).length != 0) {
        for (let cours of coursTab) {
          await this.normalizeCours(cours, (convertedCours) => {
            coursTableau.push(convertedCours);
            console.log('converti ');
            next(coursTableau);
          });
        }
      }
      else {
        this.msg = "Cet étudiant n'est inscrit à aucun cours";
      }
    });

  }

  // CREATE COURS
  createCours(cours: Cours, next) {
    let dateDebSQL = this.convertDateToSql(cours.date_deb);
    let dateFinSQL = this.convertDateToSql(cours.date_fin);
    this.request = "INSERT INTO cours (id_cours, date_deb_cours, date_fin_cours, id_horaire, no_local, id_matiere, matricule, code_UF, id_jour) VALUES(NULL, '" +
      dateDebSQL + "', '" +
      dateFinSQL + "', '" +
      cours.horaire.id_horaire + "', '" +
      cours.local.no_local + "', '" +
      cours.matiere.id_matiere + "', '" +
      cours.professeur.matricule + "', '" +
      cours.module.code_UF + "', '" +
      cours.jourSemaine.id_jour + "');";
    var url = 'http://localhost:5000/app/horaire/create';
    this.requestService.postRequest(url, this.request, (response) => {
      cours.id_cours = response.insertId;
      console.log(response.insertId);
      next(cours);
    });
  }

