  // NORMALIZE COURS : STRING => OBJECT
  async normalizeCours(coursBase: any, next) {
    let cours: Cours = new Cours();
    cours.id_cours = coursBase.id_cours;
    //cours.date_deb = this.cutDateString(coursBase.date_deb_cours);
    cours.date_deb = this.dateService.convertSQLDateToString(coursBase.date_deb_cours);

    //cours.date_fin = this.cutDateString(coursBase.date_fin_cours);
    cours.date_fin = this.dateService.convertSQLDateToString(coursBase.date_fin_cours);

    console.log('courdate ' + cours.date_deb);
    await this.getHoraireCours(coursBase.id_horaire, async (horaire) => {
      cours.horaire = new Horaire();
      cours.horaire = horaire;
      console.log('courhoraire ' + cours.horaire.id_horaire);
      await this.getLocalCours(coursBase.no_local, async (local) => {
        cours.local = new Local();
        cours.local = local;
        await this.getMatiereCours(coursBase.id_matiere, (matiere) => {
          cours.matiere = new Matiere();
          cours.matiere = matiere;
          this.getProfesseurCours(coursBase.matricule, async (professeur) => {
            cours.professeur = new Professeur();
            console.log("nom prof "+professeur.nom);
            cours.professeur = professeur[0];
            await this.getModuleCours(coursBase.code_UF, async (module) => {
              cours.module = new Module();
              cours.module = module;
              console.log('coursmodule ' + cours.module.intitule);
              await this.getJourSemaineCours(coursBase.id_jour, async (jourSemaine) => {
                cours.jourSemaine = new JourSemaine();
                cours.jourSemaine = jourSemaine;
                await next(cours);
              });
            });
          });
        });
      });
    });
  }



