  // trier cours par code
  coursTriParCodeFormation(coursTab: Array<Cours>) {
    let coursTri = new Array<Cours>();
    //recherche par min
    let min = new Cours();
    let save = new Cours();
    for (let i = 0; i < coursTab.length; i++) {
      for (let j = i; j < coursTab.length; j++) {
        min = coursTab[i];
        if (parseInt(coursTab[j].module.code_UF) <= parseInt(min.module.code_UF)) {
          save = min;
          min = coursTab[j];
          coursTab[j] = save;
        }
        coursTab[i] = min;
      }
    }
    coursTri = coursTab;
    return coursTri;
    // Tester si le tri a fonctionné...
  }

  // Tri cours par nom de module
  coursTriParNomFormation(coursTab: Array<Cours>) {
    let coursTri = new Array<Cours>();
    //recherche par min
    let min = new Cours();
    let save = new Cours();
    for (let i = 0; i < coursTab.length; i++) {
      for (let j = i; j < coursTab.length; j++) {
        min = coursTab[i];
        if (coursTab[j].module.intitule <= min.module.intitule) {
          save = min;
          min = coursTab[j];
          coursTab[j] = save;
        }
        coursTab[i] = min;
      }
    }
    coursTri = coursTab;
    return coursTri;
    // Tester si le tri a fonctionné...
  }

  convertDateToSql(date: string) {
    let dateSQL = date.substring(6, 10).concat("-").
      concat(date.substring(3, 5)).concat("-").concat(date.substring(0, 2));
    return dateSQL;
  }

  cutDateString(date: string) {
    let dateCutted = date.substring(0, 10);
    let adjust = Number(dateCutted.substring(8, 10)) + 1;
    let dateFormated = adjust.toString() + "-" + dateCutted.substring(5, 7) + "-" + dateCutted.substring(0, 4);
    return dateFormated;
  }

  cutDateStringNotAdjusted(date: string) {
    let dateCutted = date.substring(0, 10);
    let adjust = Number(dateCutted.substring(8, 10)) + 1;
    let dateFormated = dateCutted.substring(8, 10) + "-" + dateCutted.substring(5, 7) + "-" + dateCutted.substring(0, 4);
    return dateFormated;
  }

