import { Document } from './../document';
import { TypeDoc } from './../type.doc';
import { Etudiant } from './../etudiant';
import { Component, OnDestroy } from '@angular/core';
import { EtudiantService } from '../../services/etudiant.service';
import { DocumentService } from '../../services/document.service';
import { TypeDocService } from '../../services/type.doc.service';
import { Observable } from 'rxjs/Observable';
import { Subscription } from 'rxjs/Subscription';
import { Array } from 'es6-shim';

@Component({
  selector: 'app-document-form',
  templateUrl: './document-form.component.html',
  styleUrls: ['../../styles/form-style.css']
})

export class DocumentFormComponent implements OnDestroy{

  document: Document;
  etudiant = new Etudiant();
  typeDoc: TypeDoc;
  length: number;
  affiche: boolean;
  documents = new Array<Document>();
  typeDocs = new Array<TypeDoc>();
  msg: string;
  createdEtu: boolean;
  subscription: Subscription;
  constructor(private etudiantService: EtudiantService, private documentService: DocumentService, private typeDocService: TypeDocService) {
    this.document = new Document();
    // Si l'étudiant existe
    if (this.etudiantService.createdEtu) {
      if (this.etudiant.id_etudiant == this.etudiantService.savedEtudiant.id_etudiant) {
        this.etudiant = this.etudiantService.savedEtudiant;
        this.document.etudiant = this.etudiantService.savedEtudiant;

        this.documents = new Array<Document>();
          this.afficherDoc(this.etudiantService.savedEtudiant.id_etudiant);
      }
      else {
        this.documents = new Array<Document>();
        //this.documents.slice(0, this.documents.length);
        this.afficherDoc(this.etudiantService.savedEtudiant.id_etudiant); // appel de l'affichage des documents
        console.log("longueur " + this.documents.length);
        
          this.subscription = this.etudiantService.getMessage().subscribe(message => {
            this.msg = message.text; this.createdEtu = message.createdEtu;
            if (this.createdEtu == false) {
              this.reinitialiser();
              this.documents = new Array<Document>();
              this.afficherDoc;
            }
          });
      }
    }
    else {
      this.msg = "Veuillez d'abord sélectionner ou encoder un étudiant";
    }
    this.typeDocService.getAllTypeDoc((tDocs) => {
      this.typeDocs = tDocs;
    });
  }


  afficherDoc(idEtu: number) {
    this.documents = new Array<Document>();
    if (this.etudiant.id_etudiant != this.etudiantService.savedEtudiant.id_etudiant) {
      this.documentService.getDocumentByIdEtu(idEtu.toString(), (docs) => {
        if (docs != null) {
          this.documents = docs;
          this.affiche = true;
        }
        else {
          this.msg = "Aucun document à afficher";
        }
      });
    }
  }

  reinitialiser() {
    this.document = new Document();
    this.document.etudiant = new Etudiant();
    this.document.type_doc = new TypeDoc();
  }

  onSubmit() {
    this.documentService.createDocument(this.document, (doc) => {
      this.document = doc;
      this.afficherDoc(this.etudiantService.savedEtudiant.id_etudiant);
      this.msg = "Document créé";
    });
  }

  update() {
    this.document.etudiant = this.etudiant;
    this.documentService.updateDocument(this.document, () => {
      this.afficherDoc(this.etudiantService.savedEtudiant.id_etudiant);
      this.msg = "Document mis à jour";
    });
  }

  modifier(index: number) {
    this.document = this.documents[index];
  }

  supprimer(index: number) {
    this.documentService.deleteDocument(this.documents[index].id_document, async () => {
      await this.afficherDoc(this.etudiantService.savedEtudiant.id_etudiant);
      this.msg = "Document supprimé";
    });
  }

  reinitialiserDocuments() {
    this.documents = new Array<Document>();
  }

  changeMsg(message) {
    console.log(this.createdEtu);
    this.createdEtu = message.createdEtu;
    this.msg = message.text;
  }

  ngOnDestroy() {
    // unsubscribe to ensure no memory leaks
    if (this.subscription) {
      this.subscription.unsubscribe();
    }
  }
}



