import { Component, Input, OnInit, OnDestroy } from '@angular/core';
import { Router, RouterModule, ActivatedRoute } from '@angular/router';
//import { MysqlConnectService } from '../services/mysql.connect.service';
import { Etudiant } from '../../../../models/etudiant';
import { HttpClient } from '@angular/common/http';
import { EtudiantService } from '../../../../services/etudiant.service';
import { DocumentService } from '../../../../services/document.service';
import { InscriptionService } from '../../../../services/inscription.service';

@Component({
  selector: 'etats-inscriptions-component',
  templateUrl: './etats.inscriptions.html',
  styleUrls: ['../../../../styles/form-style.css']
})
export class EtatsInscriptionsComponent{

  public etudiants: Etudiant[];
  //etudiant: Etudiant;
  @Input() id: string;
  @Input() nom: string;
  @Input() prenom: string;
  empty: number;
  public message: string;
  documentsEnOrdre = new Array<boolean>();


  constructor(
    private router: Router,
    private http: HttpClient,
    private etudiantService: EtudiantService,
    private documentService: DocumentService,
    private inscriptionService: InscriptionService
  ) { this.afficherEtu(); }

  public afficherEtu() {
    this.message = "";
    this.etudiantService.getAllEtudiants((data) => {
      this.etudiants = data;
      this.documentsEnOrdre.length = 0;
      for (let i = 0; i < this.etudiants.length; i++) {
        this.documentService.getDocumentByIdEtuEnOrdre(this.etudiants[i].id_etudiant.toString(), (ordre) => {
          this.documentsEnOrdre[i] = ordre;
        });
      }
    });
  }

  public rechercherParId() {
    this.empty = 0;
    this.message = "";
    this.etudiantService.getEtudiantById(this.id, (result) => {
      this.etudiants = result;
      this.empty = result.length;
    });
    if (this.empty == 0)  { this.message = "Aucun étudiant trouvé avec cet identifiant..."; }
  }

  public rechercherParNom() {
    this.empty = 0;
    this.message = "";
    this.etudiantService.getEtudiantByNom(this.nom, (result) => {
      this.etudiants = result;
      this.empty = result.length;
    });
    if (this.empty == 0) { this.message = "Aucun étudiant trouvé avec ce nom..."; }
  }

  public modifier(index: number) {
    console.log(this.etudiants[index]);
    this.etudiantService.normalizeAndKeepThisEtudiant(this.etudiants[index], () => {
      this.router.navigateByUrl('inscriptions/encoder');
    });
  }

  public supprimer(index: number) {
    this.etudiantService.deleteEtudiant(this.etudiants[index], async () => {
      await this.afficherEtu();
    });
  }

  public inscrire(index: number) {
    console.log(this.etudiants[index]);
    this.etudiantService.normalizeAndKeepThisEtudiant(this.etudiants[index], () => {
      this.router.navigateByUrl('inscriptions/inscrire');
    });
  }



}



