import { OrganisationModule } from './organisationModule';
import { Etudiant } from './etudiant';
import { Cours } from './cours';

export class Inscription {

  public id_inscription: number;
  public date_inscription: string;
  public resultat: number;
  public notes_sup: string;
  public titre: boolean;
  public test_admission: boolean;
  public etudiant: Etudiant;
  public cours: Cours;

  constructor() {
    this.resultat = 0;
    this.notes_sup = "";
    this.titre = false;
    this.test_admission = false;
    this.cours = new Cours();
    this.etudiant = new Etudiant();
  }
}



