import { Component, ViewChild, ElementRef, Renderer2 } from '@angular/core';
import { Cours } from '../../../../models/cours';
import { Module } from '../../../../models/module';
import { Section } from '../../../../models/section';
import { Etudiant } from '../../../../models/etudiant';
import { Professeur } from '../../../../models/professeur';
import { Inscription } from '../../../../models/inscription';
import { Liste } from '../../../../models/liste';
import * as jsPDF from 'jspdf';
//import * as html2canvas from '../../jspdf/html2canvas';
import * as html2canvas from 'html2canvas';

// SERVICES
import { RequestService } from '../../../../services/request.service';
import { CoursService } from '../../../../services/cours.service';
import { ModuleService } from '../../../../services/module.service';
import { SectionService } from '../../../../services/section.service';
import { EtudiantService } from '../../../../services/etudiant.service';
import { ProfesseurService } from '../../../../services/professeur.service';
import { InscriptionService } from '../../../../services/inscription.service';
import { RapportService } from '../../../../services/rapport.service';
import { CalendarService } from '../../../../services/calendar.service';
import { DateService } from '../../../../services/date.service';

@Component({
  selector: 'listes',
  templateUrl: './listes.html'
})

export class ListesComponent {
  msg: string;
  @ViewChild('t') t: ElementRef;
  @ViewChild('content') content: ElementRef;
  coursTab = new Array<Cours>();
  liste = new Array<Liste>();
  coursUnique = new Cours();
  datesTab = new Array<Date>();
  tableList: string;

  constructor(
    private requestService: RequestService,
    private coursService: CoursService,
    private moduleService: ModuleService,
    private sectionService: SectionService,
    private etudiantService: EtudiantService,
    private professeurService: ProfesseurService,
    private inscriptionService: InscriptionService,
    private renderer: Renderer2,
    private rapportService: RapportService,
    private calendarService: CalendarService,
    private dateService: DateService
  ) {
    this.coursService.getAllCours((coursTab) => { this.coursTab = coursTab; });
  }

  async selectedCours(idCours) {
    //this.coursTab[index].id_cours;
    console.log("test change" + idCours);
    this.rapportService.getListeEtudiantsParCours(idCours, (liste) => {
      this.liste = liste;
      console.log(this.liste);
    });
    await this.rapportService.getInfoCours(idCours, async (cours) => {
      this.coursUnique = cours;
      await console.log(cours.professeur);
      //this.coursUnique.
    });
    await this.coursService.getCoursById(idCours, (cours) => {
      let convertedDD = this.dateService.cutDateString2(cours.date_deb);
      let convertedDF = this.dateService.cutDateString2(cours.date_fin);
      console.log("converted DD " + convertedDD);
      console.log("cours " + cours.jourSemaine.nom_jour);
      this.dateService.getDaysBetweenDates(convertedDD, convertedDF, cours.jourSemaine.id_jour, (tabDates) => {
        this.datesTab = tabDates;
        for (let date of this.datesTab) {
          console.log("tab dates "+date);
        }
        this.createTableHtml();
      });
    });
  }

  afficher(index: number) {
    console.log("index "+this.coursTab[index].id_cours);
  }

  public downloadPDF() {

    let doc = new jsPDF('l', 'mm', 'a4');
    let height = (this.liste.length * 10)+20;
    html2canvas(document.getElementById("maTable")).then(canvas => {
      let img = canvas.toDataURL("image/png", 1.0);
      doc.addImage(img, "JPEG", 10, 10, 280, height);
      doc.save('test.pdf');
    })
  }

  createTableHtml() {
    let lines = new Array<string>();
    let cells: string = "";
    let headerCells: string = "";

    console.log("taille cellules " + this.datesTab.length);
    headerCells = "<th> Etudiants </th>"
    for (let j = 0; j < this.datesTab.length; j++) {
      console.log("taille cellules " + this.datesTab.length);
      headerCells += "<th  align='center'>" + this.datesTab[j].toLocaleString().substring(0,5) + "</th>";
      cells += "<td></td>";
    }
    for (let i = 0; i < this.liste.length; i++) {
      lines[i] = "<tr>" + "<td>" + this.liste[i].nom + " " + this.liste[i].prenom + "</td>" + cells + "</tr>";
      //console.log("ligne " + cells);
    }
    
    let table = "<table border='1px' width='100%' cellpadding='5px'><thead>" + headerCells + "</thead><tbody>";
    for (let i = 0; i < this.liste.length; i++) {
      table += lines[i];
    }
    table += "</tbody></table>";
    this.tableList = table;
    console.log(table);
    let change_this;
  }
}



