import { Component } from '@angular/core';

@Component({
  selector: 'navbar-professeurs',
  templateUrl: './navbar.html',
  styleUrls: ['./style.css']
})
export class NavbarProfesseurs {
  isExpanded = false;
  collapse() {
    this.isExpanded = false;
  }

  toggle() {
    this.isExpanded = !this.isExpanded;
  }
}



