import { Component, Input, OnInit, OnDestroy } from '@angular/core';
import { Router, RouterModule, ActivatedRoute } from '@angular/router';
import { Module } from '../../../../models/module';
import { HttpClient } from '@angular/common/http';
import { ModuleService } from '../../../../services/module.service';
import { ACompose } from '../../../../models/aCompose';
import { Section } from '../../../../models/section';
import { AComposeService } from '../../../../services/a.compose.service';
import { SectionService } from '../../../../services/section.service';

@Component({
  selector: 'modules-rechercher',
  templateUrl: './rechercher.modules.html',
  styleUrls: ['../../../../styles/form-style.css']
})

export class RechercherModulesComponent {

  @Input() code: string;
  @Input() no: string;
  module = new Module();
  modules: Module[];
  submitted = false;
  aCompose = new ACompose();
  //aComposeTab = new Array<ACompose>();
  aComposeTab: ACompose[];
  sections: Section[];
  msg: string;

  constructor(
    private moduleService: ModuleService,
    private sectionService: SectionService,
    private aComposeService: AComposeService,
    private router: Router) {

    this.aCompose.module = new Module();
    this.aCompose.section = new Section();
    this.sectionService.getAllSections((data) => {
      this.sections = data;
    });
    if (this.aComposeService.saved == true) {
      this.aCompose = this.aComposeService.savedACompose;
    }
    this.afficherModules();
  }

  async afficherModules() {
    await this.aComposeService.constructAllACompose((data) => {
      this.aComposeTab = data;
    });
  }

  rechercherParCode() {
    this.msg = "";
    let aComposeTmp = new Array<ACompose>();
    for (let i = 0; i < this.aComposeTab.length; i++) {
      if (this.aComposeTab[i].module.code_UF == this.code) {
        aComposeTmp.push(this.aComposeTab[i]);
      }
    }
    if (aComposeTmp.length == 0) {
      this.msg = "Aucune UF ne possède ce code";
    }
    else {
      this.aComposeTab = aComposeTmp;
    }
    //if (this.empty == 0) { this.message = "Aucun étudiant trouvé avec cet identifiant..."; }
  }

  rechercherParNo() {
    this.msg = "";
    this.moduleService.getModuleByNo(this.no, (result) => {
      this.modules = result;
    });
  }

  public modifier(index: number) {
    console.log(this.aComposeTab[index]);
    this.aComposeService.savedACompose = this.aComposeTab[index];
    this.aComposeService.saved = true;
    this.router.navigateByUrl('modules/ajouter');
  }

  public supprimer(index: number) {
    this.aComposeService.deleteACompose(this.aComposeTab[index], (rep1) => {
      if (Object.keys(rep1).length > 0) {
        this.moduleService.deleteModule(this.aComposeTab[index].module, () => {
          this.afficherModules();
        });
      }
    });
  }

}



