import { Component } from '@angular/core';
import { Router, RouterModule, ActivatedRoute } from '@angular/router';
import { Globals } from 'globals';
import { Cours } from '../../../../models/cours';
import { CoursService } from '../../../../services/cours.service';
/*import { HoraireService } from '../../../../services/horaire.service';
import { LocalService } from '../../../../services/local.service';
import { ModuleService } from '../../../../services/module.service';
import { MatiereService } from '../../../../services/matiere.service';
import { JourSemaineService } from '../../../../services/jour.semaine.service';
import { ProfesseurService } from '../../../../services/professeur.service';
import { Horaire } from '../../../../models/horaire';
import { Local } from '../../../../models/local';
import { Module } from '../../../../models/module';
import { Matiere } from '../../../../models/matiere';
import { JourSemaine } from '../../../../models/jourSemaine';
import { Professeur } from '../../../../models/professeur';*/

@Component({
  selector: 'cours-rechercher',
  templateUrl: './cours.rechercher.html'
})

export class CoursRechercherComponent {

  public message: string;
  coursTab = new Array<Cours>();

  constructor(private router: Router, private coursService: CoursService) {
    this.afficherCours();
  }

  public afficherCours() {
    this.message = "";
    this.coursService.getAllCours((data) => {
      this.coursTab = data;
    });
  }

  modifier(index: number) {
    this.coursService.savedCours = this.coursTab[index];
    this.coursService.saved = true;
    this.router.navigateByUrl('cours/creer');
  }

  supprimer(index: number) {
    this.coursService.deleteCours(this.coursTab[index].id_cours, () => {
      this.afficherCours();
    });
  }
}



