import { Component, Input, OnInit } from '@angular/core';
import { Professeur } from '../../../../models/professeur';
import { ProfesseurService } from '../../../../services/professeur.service';
import { Router, RouterModule, ActivatedRoute } from '@angular/router';
//import { ModalService } from '../../../../services/modal.service';
//import { ModalComponent } from '../../../modal/modal.component';
import { Globals } from 'globals';

@Component({
  moduleId: module.id.toString(),
  selector: 'professeurs-encoder-component',
  templateUrl: './professeurs.encoder.html',
  styleUrls: ['../../../../styles/form-style.css']
})
export class ProfesseursEncoderComponent /*implements OnInit*/ {
  @Input() matricule: string;
  @Input() nom: string;
  @Input() prenom: string;
  empty: number;
  public message: string;
  professeur = new Professeur();
  professeurs = new Array<Professeur>();
  msg: string;
  constructor(private router: Router, private professeurService: ProfesseurService/*, private modalService: ModalService*/) {
    //this.professeur = new Professeur();
    if (this.professeurService.saved == true) {
      this.professeur == this.professeurService.savedProfesseur;
    }
    this.afficherProfs();
  }

  onSubmit() {
    if (!this.professeurService.saved) {
      this.professeurService.createProfesseur(this.professeur, (response) => {
        if (Object.keys(response).length > 0) {
          this.msg = "Professeur créé avec succès";
        }
        else {
          this.msg = "Le professeur n'a pu être enregistré";
        }
        this.afficherProfs();
      });
    }
    else {
      this.update();
    }
  }

  afficherProfs() {
    this.professeurService.getAllProfesseurs((response) => {
      this.professeurs = response;
    });
  }

  modifier(i: number) {
    this.professeurService.savedProfesseur = this.professeurs[i];
    this.professeur = this.professeurService.savedProfesseur;
    this.professeurService.saved = true;
  }

  supprimer(i) {
    //this.modalService.open('custom-modal-1');
    this.msg = "";
    this.professeurService.deleteProfesseur(this.professeurs[i], (deleted) => {
      if (deleted != null) {
        this.msg = "Professeur supprimé";
      }
      else{
        this.msg = "Ce professeur ne peut être supprimé car il est défini dans des cours...";
      }
      this.afficherProfs();
    });
  }

  update() {
    this.professeurService.updateProfesseur(this.professeur, () => {
      this.msg = "Professeur mis à jour"
      this.professeurService.saved = false;
    });
  }

  reinitialiser() {
    this.professeur = new Professeur();
    this.professeurService.saved = false;
  }

  rechercherParMatricule() {
    this.empty = 0;
    this.msg = "";
    this.professeurService.getProfesseurByMatricule(this.matricule, (result) => {
      if (Object.keys(result).length != 0) {
      
        this.professeurs = new Array<Professeur>();
        this.professeurs.push(result[0]);
        this.empty = Object.keys(result).length;
      }
      if (this.empty == 0) { this.msg = "Aucun professeur trouvé..."; }
    });
  }

  rechercherParNom() {
    this.empty = 0;
    this.msg = "";
    this.professeurService.getProfesseurByNom(this.nom, (result) => {
      this.professeurs = result;
      this.empty = Object.keys(result).length;
      if (this.empty == 0) { this.msg = "Aucun professeur trouvé..."; }
    });
  }
}



