import { Component } from '@angular/core';

@Component({
  selector: 'navbar-inscriptions',
  templateUrl: './navbar.html',
  styleUrls: ['./style.css']
})
export class NavbarComponent {
  isExpanded = false;
  collapse() {
    this.isExpanded = false;
  }

  toggle() {
    this.isExpanded = !this.isExpanded;
  }
}



