import { Role } from '../models/role';

export class User {
  id_user: number;
  username: string;
  password: string;
  user_nom: string;
  user_prenom: string;
  email: string;
  date_creation: string;
  role: Role;
  constructor() {
    this.username = "";
    this.username = "";
    this.user_nom = "";
    this.user_prenom = "";
    this.email = "";
    this.role = new Role();
  }
}



