import { Injectable } from '@angular/core';
import { RequestService } from './request.service';
import { HttpClient } from '@angular/common/http';
import { Cours } from '../models/cours';
import { HoraireService } from './horaire.service';
import { LocalService } from './local.service';
import { MatiereService } from './matiere.service';
import { ProfesseurService } from './professeur.service';
import { ModuleService } from './module.service';
import { JourSemaineService } from './jour.semaine.service';
import { Horaire } from '../models/horaire';
import { Local } from '../models/local';
import { Matiere } from '../models/matiere';
import { Professeur } from '../models/professeur';
import { Module } from '../models/module';
import { JourSemaine } from '../models/jourSemaine';
import { Liste } from '../models/liste';
import { CoursService } from './cours.service';

@Injectable()
export class RapportService {

  msg: string;
  request: string;
  savedCours: Cours;
  saved: boolean = false;
  cours = new Cours();

  constructor(
    private requestService: RequestService,
    private horaireService: HoraireService,
    private localService: LocalService,
    private matiereService: MatiereService,
    private professeurService: ProfesseurService,
    private moduleService: ModuleService,
    private JourSemaineService: JourSemaineService,
    private CoursService: CoursService
  ) { }


  getListeEtudiantsParCours(idCours: number, next) {
    var url = 'http://localhost:5000/app/rapport/cours/' + idCours;
    this.requestService.getRequestByUrl(url, (listeEtu) => {
      next(listeEtu);
    });
  }

  getListeEtudiantsNormalParCours(idCours: number, next) {
    var url = 'http://localhost:5000/app/rapport/cours/normal/' + idCours;
    this.requestService.getRequestByUrl(url, (listeEtu) => {
      next(listeEtu);
    });
  }

  getInfoCours(idCours, next) {
    this.CoursService.getCoursByIdListe(idCours, (cours) => {
      next(cours);
    });
  }

 /*async getAllCours(next) {
    let coursTableau = new Array <Cours>();
    var url = 'http://localhost:5000/app/cours/';
    await this.requestService.getRequestByUrl(url, async (data) => {
      for (let cours of data) {
        await this.normalizeCours(cours, (convertedCours) => {
          coursTableau.push(convertedCours);
          console.log('converti ');
          next(coursTableau);
        });
      }
    });
  }

  async getCoursById(idCours: number, next) {
    var url = 'http://localhost:5000/app/cours/id/' + idCours;
    await this.requestService.getRequestByUrl(url, async (data) => {
      console.log('cours par id' + data);
      this.normalizeCours(data, async (normalizedCours) => {
        await next(normalizedCours);
      });
    });
  }

  async getCoursByEtudiantId(idEtu: number, next) {
    let coursTableau = new Array<Cours>();
    var url = 'http://localhost:5000/app/cours/etudiant/' + idEtu;
    await this.requestService.getRequestByUrl(url, async (coursTab) => {
      if (Object.keys(coursTab).length != 0) {
        for (let cours of coursTab) {
          await this.normalizeCours(cours, (convertedCours) => {
            coursTableau.push(convertedCours);
            console.log('converti ');
            next(coursTableau);
          });
        }
      }
      else {
        this.msg = "Cet étudiant n'est inscrit à aucun cours";
      }
    });

  }

  // CREATE COURS
  createCours(cours: Cours, next) {
    let dateDebSQL = this.convertDateToSql(cours.date_deb);
    let dateFinSQL = this.convertDateToSql(cours.date_fin);
    this.request = "INSERT INTO cours VALUES(NULL, '" +
      dateDebSQL + "', '" +
      dateFinSQL + "', '" +
      cours.horaire.id_horaire + "', '" +
      cours.local.no_local + "', '" +
      cours.matiere.id_matiere + "', '" +
      cours.professeur.matricule + "', '" +
      cours.module.code_UF + "', '" +
      cours.jourSemaine.id_jour + "');";
    var url = 'http://localhost:5000/app/horaire/create';
    this.requestService.postRequest(url, this.request, (response) => {
      cours.id_cours = response.insertId;
      console.log(response.insertId);
      next(cours);
    });
  }

  updateCours(cours: Cours, next) {

  }

  deleteCours(idCours: number, next) {
    this.request = "DELETE FROM cours WHERE id_cours=" + idCours + ";";
    var url = "http://localhost:5000/app/cours/delete";
    this.requestService.postRequest(url, this.request, next);    
  }

  // NORMALIZE COURS : STRING => OBJECT
  async normalizeCours(coursBase: any, next) {
    let cours: Cours = new Cours();
    cours.id_cours = coursBase.id_cours;
    cours.date_deb = this.cutDateString(coursBase.date_deb_cours);
    cours.date_fin = this.cutDateString(coursBase.date_fin_cours);
    console.log('courdate ' + cours.date_deb);
    await this.getHoraireCours(coursBase.id_horaire, (horaire) => {
      cours.horaire = new Horaire();
      cours.horaire = horaire;
      console.log('courhoraire ' + cours.horaire.id_horaire);
      this.getLocalCours(coursBase.no_local, (local) => {
        cours.local = new Local();
        cours.local = local;
        this.getMatiereCours(coursBase.id_matiere, (matiere) => {
          cours.matiere = new Matiere();
          cours.matiere = matiere;
          this.getProfesseurCours(coursBase.matricule, (professeur) => {
            cours.professeur = new Professeur();
            cours.professeur = professeur;
            this.getModuleCours(coursBase.code_UF, (module) => {
              cours.module = new Module();
              cours.module = module;
              console.log('coursmodule ' + cours.module.intitule);
              this.getJourSemaineCours(coursBase.id_jour, (jourSemaine) => {
                cours.jourSemaine = new JourSemaine();
                cours.jourSemaine = jourSemaine;
                next(cours);
              });
            });
          });
        });
      });
    });
  }


  // GET NESTED CLASSES

  getHoraireCours(id_horaire: number, next) {
    this.horaireService.getHoraireById(id_horaire, (horaire) => {
      next(horaire);
    });
  }

  getLocalCours(no_local: number, next) {
    this.localService.getLocalById(no_local, (local) => {
      next(local);
    });
  }

  getMatiereCours(id_matiere: number, next) {
    this.matiereService.getMatiereById(id_matiere, (matiere) => {
      next(matiere);
    });
  }

  getProfesseurCours(matricule: string, next) {
    this.professeurService.getProfesseurByMatricule(matricule, (professeur) => {
      next(professeur);
    });
  }

  getModuleCours(code_UF: string, next) {
    this.moduleService.getModuleByCode(code_UF, (module) => {
      next(module[0]);
    });
  }

  getJourSemaineCours(id_jour: number, next) {
    this.JourSemaineService.getJourById(id_jour, (jourSemaine) => {
      next(jourSemaine);
    });
  }

  testIfCoursExistAlready(cours: Cours, next) {
    let dateDeb = this.convertDateToSql(cours.date_deb);
    let dateFin = this.convertDateToSql(cours.date_fin);

    let requestLocal = "SELECT id_cours FROM cours WHERE " +
      "date_deb_cours >='" + dateDeb + "' AND " +
      "date_fin_cours <='" + dateFin + "' AND " +
      "id_horaire=" + cours.horaire.id_horaire + " AND " +
      "no_local=" + cours.local.no_local + " AND " +
      "id_jour=" + cours.jourSemaine.id_jour + ";";

    let requestProf = "SELECT id_cours FROM cours WHERE " +
      "date_deb_cours >='" + dateDeb + "' AND " +
      "date_fin_cours <='" + dateFin + "' AND " +
      "id_horaire=" + cours.horaire.id_horaire + " AND " +
      "matricule=" + cours.professeur.matricule + " AND " +
      "id_jour=" + cours.jourSemaine.id_jour + ";";

    let obj = {};
    let obj2 = {};
    obj = { "request": requestLocal };
    obj2 = { "request": requestProf };

    var urlLocal = 'http://localhost:5000/app/cours/verif/' + obj["request"];
    var urlProf = 'http://localhost:5000/app/cours/verif/' + obj2["request"];
    console.log(urlLocal);
    this.requestService.getRequestByUrl(urlLocal, (response1) => {
      if (Object.keys(response1).length == 0) {
        this.requestService.getRequestByUrl(urlProf, (response2) => {
          if (Object.keys(response2).length == 0) {
            this.createCours(cours, (res) => {
              if (Object.keys(res).length != 0) {
                this.msg = "Horaire créé !";
              }
            });
          }
          else {
            if (response2[0].id_cours == cours.id_cours) {
              this.msg = "Un autre horaire est déjà attribué à ce professeur au même moment";
              console.log("Un autre horaire est déjà attribué à ce professeur au même moment");
            }
          }
        });
      }
      else if (response1[0].id_cours == cours.id_cours)
      {
        this.msg = "Local déjà utilisé durant ce même horaire";
        console.log("Local déjà utilisé durant ce même horaire");
      }
    });
  }

  coursTriParFormation(coursTab: Array<Cours>, next) {
    let coursTri = new Array<Cours>();
    //recherche par min
    let min = new Cours();
    let save = new Cours();
    for (let i = 0; i < coursTab.length; i++) {
      for (let j = i; j < coursTab.length; j++) {
        min = coursTab[i];
        if (parseInt(coursTab[j].module.code_UF) <= parseInt(min.module.code_UF)) {
          save = min;
          min = coursTab[j];
          coursTab[j] = save;
        }
      }
    }
    coursTri = coursTab;
    next(coursTri);
    // Tester si le tri a fonctionné...
  }

  convertDateToSql(date: string) {
    let dateSQL = date.substring(6, 10).concat("-").
      concat(date.substring(3, 5)).concat("-").concat(date.substring(0, 2));
    return dateSQL;
  }

  cutDateString(date: string) {
    let dateCutted = date.substring(0, 10);
    let adjust = Number(dateCutted.substring(8, 10)) + 1;
    let dateFormated = adjust.toString() + "-" + dateCutted.substring(5, 7) + "-" + dateCutted.substring(0, 4);
    return dateFormated;
  }*/
}



