import { Component } from '@angular/core';

@Component({
  selector: 'navbar-rapports',
  templateUrl: './navbar.rapports.html',
  styleUrls: ['./style.css']
})
export class NavbarRapportsComponent {
  isExpanded = false;
  collapse() {
    this.isExpanded = false;
  }

  toggle() {
    this.isExpanded = !this.isExpanded;
  }
}



