import { Component, Renderer2, ViewChild, ElementRef, AfterViewInit } from '@angular/core';
import { Ville } from '../../../../models/ville';
import { VilleService } from '../../../../services/ville.service';
import { Pays } from '../../../../models/pays';
import { PaysService } from '../../../../services/pays.service';

@Component({
  selector: 'villes',
  templateUrl: './ville.html'
})

export class DonneesVilleComponent {

  @ViewChild('p') p: ElementRef;
  villes: Ville[];
  paysTab: Pays[];
  ville = new Ville();

  constructor(private villeService: VilleService, private paysService: PaysService, private renderer: Renderer2) {
    this.paysService.getAllPays((data) => { this.paysTab = data; });

    this.afficherVilles();
  }

  createVille() {
    this.villeService.createVille(this.ville, (data) => { this.afficherVilles(); });
    }

  afficherVilles() {
    this.villeService.getAllVilles((data) => {
      console.log(data);

      this.villes = data;
    });
  }

  onSubmit() {
    this.createVille();
  }

  modifier(index: number) {
    this.ville = this.villes[index];
    this.ngAfterViewInit();
  }

  ngAfterViewInit() {
    console.log(this.p.nativeElement.value);
  }

  update() {
    this.villeService.updateVille(this.ville, () => { this.afficherVilles(); });
  }

  delete(index: number) {
    this.villeService.deleteVille(this.villes[index], () => { this.afficherVilles(); })
  }
}



