import { Injectable, OnDestroy } from '@angular/core';
import { RequestService } from './request.service';
import { HttpClient } from '@angular/common/http';
import { Etudiant } from '../models/etudiant';
import { EtudiantService } from '../services/etudiant.service';
import { TypeDoc } from '../models/type.doc';
import { TypeDocService } from '../services/type.doc.service';
import { Document } from '../models/document';

@Injectable()
export class DocumentService {
  type
  documents = new Array<Document>();
  request: string;
  savedDocument: Document;
  enOrdre = new Array<boolean>();

  constructor(private requestService: RequestService, private etudiantService: EtudiantService, private typeDocService: TypeDocService) {

  }

  async getAllDocuments(idEtu, next) {
    let documentsTmp = new Array<Document>();
    let documentTmp2 = new Array<Document>();
    var url = 'http://localhost:5000/app/document/etu/' + idEtu;
    this.requestService.getRequestByUrl(url, async (data) => {
      documentsTmp = data;
      if (Object.keys(data).length > 0) {
        for (let i = 0; i < documentsTmp.length; i++) {
          await this.normalizeDocument(data[i],async(document) => {
            await this.documents.push(document);
          });
        }
        next(this.documents);
      }
      else {
        next(null);
      }
    });
  }

  getDocumentById(idDoc: number, next) {
    var url = 'http://localhost:5000/app/document/id/' + idDoc;
    this.requestService.getRequestByUrl(url, (data) => {
      next(data[0]);
    });
  }

  async getDocumentByIdEtu(idEtu: string, next) {
    let documentsTmp = new Array<Document>();
    this.documents = new Array<Document>();
    let doc: Document;
    let ordre: boolean = null;
    var url = 'http://localhost:5000/app/document/etu/' + idEtu;
    await this.requestService.getRequestByUrl(url, async (data) => {
      //documentsTmp = data;
      if (Object.keys(data).length > 0) {
        for (let i = 0; i < data.length; i++) {
          await this.normalizeDocument(data[i], (document) => {
            doc = new Document();
            doc.etudiant = new Etudiant();
            doc.type_doc = new TypeDoc();
            this.documents[i] = document;
            if (this.documents[i].valide == false) {
              this.enOrdre[i] = false;
            }
            if (i == data.length && this.enOrdre[i] != false) {
              this.enOrdre[i] = true;
            }
          });
        }
        next(this.documents);
      }
      else {
        next(null);
      }
    });
  }

  async getDocumentByIdType(idType: string, next) {
    var url = 'http://localhost:5000/app/document/type-doc/id/' + idType;
    this.requestService.getRequestByUrl(url, (data) => {
      let document = new Document();
      this.normalizeDocument(data[0], (doc) => {
        document = doc;
        next(document);
      });
    });
  }

  createDocument(document: Document, next) {
    console.log("creation doc");
    let dateRemise: string;
    let dateDebVal: string;
    let dateFinVal: string;
    if (document.date_remise.length != 0) {
      dateRemise = this.convertDateToSql(document.date_remise);
    } else {
      dateRemise = "";
    }
    if (document.date_deb_validite.length != 0) {
      dateDebVal = this.convertDateToSql(document.date_deb_validite)
    } else {
      dateDebVal = "";
    }
    if (document.date_fin_validite.length != 0) {
      dateFinVal = this.convertDateToSql(document.date_fin_validite);
    } else {
      dateFinVal = "";
    }

    this.request = 'INSERT INTO document VALUES(NULL,' +
      document.valide + ', "' +
      dateRemise + '", "' +
      dateDebVal + '", "' +
      dateFinVal + '", "' +
      document.langue + '", ' +
      document.a_payer + ', ' +
      document.montant_paye + ', ' +
      document.paye + ', ' +
      document.signature + ', ' +
      this.etudiantService.savedEtudiant.id_etudiant + ', ' +
      document.type_doc.id_type + ');';

    var url = 'http://localhost:5000/app/document/create';
    this.requestService.postRequest(url, this.request, (response) => {
      document.id_document = response.insertId;
      next(document);
    });
  }

  updateDocument(document: Document, next) {
    let dateRemise: string;
    let dateDebVal: string;
    let dateFinVal: string;
    if (document.date_remise.length != 0) {
      dateRemise = this.convertDateToSql(document.date_remise);
    } else {
      dateRemise = "";
    }
    if (document.date_deb_validite.length != 0) {
      dateDebVal = this.convertDateToSql(document.date_deb_validite)
    } else {
      dateDebVal = "";
    }
    if (document.date_fin_validite.length != 0) {
      dateFinVal = this.convertDateToSql(document.date_fin_validite);
    } else {
      dateFinVal = "";
    }
    this.request = "UPDATE document SET " +
      'valide=' + document.valide + ', ' +
      'date_remise="' + dateRemise + '", ' +
      'date_deb_validite="' + dateDebVal + '", ' +
      'date_fin_validite="' + dateFinVal + '", ' +
      'langue="' + document.langue + '", ' +
      'a_payer=' + document.a_payer + ', ' +
      'montant_paye=' + document.montant_paye + ', ' +
      'paye=' + document.paye + ', ' +
      'signature=' + document.signature + ', ' +
      'id_etudiant=' + this.etudiantService.savedEtudiant.id_etudiant + ', ' +
        'id_type=' + document.type_doc.id_type + ' WHERE id_document=' + document.id_document + ';';
    var url = 'http://localhost:5000/app/document/update';
    this.requestService.postRequest(url, this.request, () => {
      next();
    });
  }

  deleteDocument(idDoc: number, next) {
    this.request = "DELETE FROM document WHERE id_document=" + idDoc + ";";
    var url = "http://localhost:5000/app/document/delete";
    this.requestService.postRequest(url, this.request, next);
  }

  normalizeDocument(docSQL, next) {
    let document = new Document();
    document.a_payer = docSQL.a_payer;
    /*document.date_deb_validite = docSQL.date_deb_validite;
    document.date_fin_validite = docSQL.date_fin_validite;
    document.date_remise = docSQL.date_remise;*/
    document.id_document = docSQL.id_document;
    document.langue = docSQL.langue;
    document.montant_paye = docSQL.montant_paye;
    document.paye = docSQL.paye;
    document.signature = docSQL.signature;
    document.valide = docSQL.valide;
    this.etudiantService.getEtudiantById(docSQL.id_etudiant, (etu) => {
      document.etudiant = new Etudiant();    
      document.etudiant = etu;
      this.typeDocService.getTypeDocById(docSQL.id_type, (typeDoc) => {
        document.type_doc = new TypeDoc();
        document.type_doc = typeDoc;

        document.date_remise = this.cutDateString(docSQL.date_remise);
        document.date_deb_validite = this.cutDateString(docSQL.date_deb_validite);
        document.date_fin_validite = this.cutDateString(docSQL.date_fin_validite);
        next(document);
      });
    });
  }

  convertDateToSql(date: string) {
    let dateSQL = date.substring(6, 10).concat("-").
      concat(date.substring(3, 5)).concat("-").concat(date.substring(0, 2));
    return dateSQL;
  }

  cutDateString(dateSQL) {
    let date = dateSQL;
    if (Number(date.substring(0, 2) != 0)) {
      let dateCutted = date.substring(0, 10);
      let adjust = Number(dateCutted.substring(8, 10)) + 1;
      let dateForm: string;
      if (adjust < 10) {
        dateForm = "0" + adjust.toString();
      }
      else {
        dateForm = adjust.toString();
      }
      let dateFormated = dateForm + "-" + dateCutted.substring(5, 7) + "-" + dateCutted.substring(0, 4);
      //return dateFormated;
      dateSQL = dateFormated;
    }
    else {
      dateSQL = "";
    }
    return dateSQL;
  }

  resetDocs() {

  }

  async estEnOrdre(id, next) {
    let ordre = false;
    await this.getDocumentByIdEtu(id, async (documents) => {
      if (documents != null) {
        /*console.log("voici " + this.documents.length);
        if (this.documents.length > 0) {
          console.log("voici " + this.documents);
          for (let i = 0; i < this.documents.length; i++) {
            console.log("val doc " + documents[i].valide);
            if (documents[i].valide == '0') {
              ordre = true;
            }
            if (i >= Object.keys(documents).length && ordre != false) {
              ordre = true;
            }
          }
        }*/
        await console.log(this.enOrdre);
        for (let doc of documents) {
          console.log(this.enOrdre);
        }
      }
      else {
        ordre = true;
      }
      //this.enOrdre.push(ordre);
      next(ordre);
    });
  }

  async getDocumentByIdEtuEnOrdre(idEtu: string, next) {
    let documentsTmp = new Array<Document>();
    this.documents = new Array<Document>();
    let doc: Document;
    let ordre: boolean = null;
    var url = 'http://localhost:5000/app/document/etu/' + idEtu;
    await this.requestService.getRequestByUrl(url, async (data) => {
      //documentsTmp = data;
      if (Object.keys(data).length > 0) {
        for (let i = 0; i < data.length; i++) {
          await this.normalizeDocument(data[i], (document) => {
            this.documents[i] = document;
            if (this.documents.length > 0) {
              for (let j = 0; j < this.documents.length; j++) {
                console.log(this.documents[j].valide);
                if (this.documents[j].valide != true) {
                  ordre = false;
                }
                if (j == this.documents.length || ordre != false) {
                  ordre = true;
                }
              }
            }
            else {
              ordre = true;
            }
            console.log("ordre " + ordre);
            next(ordre);
          });
        }
      }
      else {
        ordre = true;
        next(ordre);
      }
    });
  }
}



