import { Injectable } from '@angular/core';
import { RequestService } from './request.service';
import { HttpClient } from '@angular/common/http';

@Injectable()
export class JourSemaineService {

  request: string;

  constructor(private requestService: RequestService) { }

  getAllJours(next) {
    var url = 'http://localhost:5000/app/jour/';
    this.requestService.getRequestByUrl(url, (data) => {
      next(data);
    });
  }

  getJourById(idJour: number, next) {
    var url = 'http://localhost:5000/app/jour/id/' + idJour;
    this.requestService.getRequestByUrl(url, (jour) => {
      next(jour[0]);
    });
  }


  getJourByNom(nomJour: string, next) {
    var url = 'http://localhost:5000/app/jour/nom/' + nomJour;

    this.requestService.getRequestByUrl(url, (jour) => {
      next(jour);
    });
  }
}



