import { Component, Input } from '@angular/core';
import { LocalService } from '../../../../services/local.service';
import { Local } from '../../../../models/local';

@Component({
  selector: 'local',
  templateUrl: './local.html'
})

export class DonneesLocalComponent {

  locaux: Local[];
  local = new Local();

  constructor(private localService: LocalService) { this.afficherLocaux(); }

  createLocal() {
    this.localService.createLocal(this.local, () => { this.afficherLocaux(); });
  }

  afficherLocaux() {
    this.localService.getAllLocaux((data) => {
      console.log(data);
      this.locaux = data;
    })
  }

  onSubmit() {
    this.createLocal();
  }

  modifier(index: number) {
    this.local = this.locaux[index];
  }

  update() {
    this.localService.updateLocal(this.local, () => { this.afficherLocaux(); })
  }
}



