import { Injectable } from '@angular/core';
import { RequestService } from './request.service';
import { HttpClient } from '@angular/common/http';

@Injectable()
export class DateService {
  request: string;
  constructor() { }

  convertDateToSql(date: string) {
    let dateSQL = date.substring(6, 10).concat("-").
      concat(date.substring(3, 5)).concat("-").concat(date.substring(0, 2));
    return dateSQL;
  }

  cutDateString2(date: string) {
    let dateForm = new Date(Date.parse(this.convertDateToSql(date)));
    return dateForm;
  }

  cutDateString(date: string) {
    let dateCutted = date.substring(0, 10);
    let result = Number(dateCutted.substring(8, 10)) + 1;
    let adjust: string;
    if (result < 10) { adjust = "0" + result.toString(); }
    else {
      adjust = result.toString();
    }
    let dateFormated = adjust + "-" + dateCutted.substring(5, 7) + "-" + dateCutted.substring(0, 4);
    //return dateFormated;
    date = dateFormated;
    return date;
  }

  cutDateStringWithoutAdjust(date: string) {
    let dateCutted = date.substring(0, 10);
    let result = Number(dateCutted.substring(8, 10)) + 1;
    let adjust: string;
    if (result < 10) { adjust = "0" + result.toString(); }
    else {
      adjust = result.toString();
    }
    let dateFormated = adjust + "-" + dateCutted.substring(5, 7) + "-" + dateCutted.substring(0, 4);
    //return dateFormated;
    date = dateFormated;
    return date;
  }

  convertToInputString(stringDate: string) {
    let currentDate = new Date();
    currentDate = new Date(Date.parse(this.convertDateToSql(stringDate)));
    return currentDate.toISOString().substring(0, 10);
  }

  testValidity(date: string) {
    let valide = false;
    if (date.indexOf("/") == 2 && date.lastIndexOf("/") == 5 || date.indexOf("-") == 2 && date.lastIndexOf("-") == 5) {
      console.log("test 1");
      valide = this.checkDay(date.substring(0, 2), date.substring(3, 5), date.substring(6, 10));
    }
    if (date.indexOf("/") == 1 && date.lastIndexOf("/") == 3 || date.indexOf("-") == 1 && date.lastIndexOf("-") == 3) {
      console.log("test 2");
      valide = this.checkDay(date.substring(0, 1), date.substring(2, 3), date.substring(4, 8));
    }
    return valide;
  }

  checkDay(day, month, year) {
    console.log(day + "-" + month + "-" + year);
    let d = parseInt(day);
    let m = parseInt(month);
    let y = parseInt(year);
    let valide = false;
    if (y < new Date().getFullYear() && year.length == 4) {
      if (m == 1 || m == 3 || m == 5 || m == 7 || m == 8 || m == 10 || m == 12) {
        if (day <= 31) { valide = true; }
      }
      if (m == 4 || m == 6 || m == 9 || m == 11) {
        if (day <= 30) { valide = true; }
      }
      if (m == 2) {
        if (y % 4 == 0 && y % 100 != 0 || y % 400 == 0) {
          if (day <= 29) { valide = true; }
        }
        else if (day <= 28) { valide = true; }
      }
    }
    return valide;
  }

  getDaysBetweenDates(dateDeb, dateFin, jour, next) {
    var dates = [],
      currentDate = dateDeb,
      addDays = function (days) {
        var date = new Date(this.valueOf());
        date.setDate(date.getDate() + days);
        return date;
      };

    for (let i = 0; i < 7; i++) {
      if (addDays.call(currentDate, i).getDay() == jour) {
        currentDate = addDays.call(currentDate, i);
      }
    }

    while (currentDate <= dateFin) {
      dates.push(currentDate);
      currentDate = addDays.call(currentDate, 7);
    }
    next(dates);
  }

  cutDateStringNotAdjusted(date: string) {
    let dateCutted = date.substring(0, 10);
    let adjust = Number(dateCutted.substring(8, 10)) + 1;
    let dateFormated = dateCutted.substring(8, 10) + "-" + dateCutted.substring(5, 7) + "-" + dateCutted.substring(0, 4);
    return dateFormated;
  }

  convertSQLDateToString(date) {
    let d = date.substring(8, 10);
    let m = date.substring(5, 7);
    let y = date.substring(0, 4);
    return this.adjustDate(d, m, y);
  }

  adjustDate(day, month, year) {
    console.log(day + "-" + month + "-" + year);
    let d = parseInt(day);
    let m = parseInt(month);
    let y = parseInt(year);
    let valide = false;

    if (d <= 27) {
      d++;
    }
    else if (d == 28) {
      if (m == 2) {
        if (y % 4 == 0 && y % 100 != 0 || y % 400 == 0) {
          d++;
        }
        else {
          m++;
          d = 1;
        }
      }
      else {
        d++;
      }
    }
    else if (d == 29) {
      if (m == 2) {
        m++;
        d = 1;
      }
      else {
        d++;
      }
    }
    else if (d == 30) {
      if (m == 4 || m == 6 || m == 9 || m == 11) {
        m++;
        d = 1;
      }
      else {
        d++;
      }
    }
    else if (d == 31) {
      if (m != 12) {
        m++;
        d = 1;
      }
      else {
        y++;
        m = 1;
        d = 1;
      }
    }

    let dConverted;
    let mConverted;
    if (d < 10) {
      dConverted = "0" + d.toString();
    }
    else {
      dConverted = d.toString();
    }
    if (m < 10) {
      mConverted = "0" + m.toString();
    }
    else {
      mConverted = m.toString();
    }

    let dateStringConverted = dConverted + "-" + mConverted + "-" + y.toString();
    return dateStringConverted;
  }
}



