import { Component } from '@angular/core';
import { Pays } from '../pays';


@Component({
  selector: 'app-pays-form',
  templateUrl: './pays-form.component.html'
})
export class PaysFormComponent {
  submitted = false;
  pays = new Pays();

  onSubmit() {
    this.submitted = true;
    console.log(this.pays.nom_pays);
  }
}



