import { Component } from '@angular/core';
import { Section } from '../../../../models/section';
import { SectionService } from '../../../../services/section.service';

@Component({
  selector: 'gestion-sections',
  templateUrl: './gestion.sections.html',
  styleUrls: ['../../../../styles/form-style.css']
})

export class GestionSectionsComponent {
  msg: string;
  section: Section = new Section();
  sections: Section[];

  constructor(private sectionService: SectionService) {
    this.afficherSections();
  }

  afficherSections() {
    this.sectionService.getAllSections((sections) => {
      this.sections = sections;
    });
  }

  onSubmit() {
    this.sectionService.createSection(this.section.nom_section, (data) => {
      if (Object.keys(data).length > 0) {
        this.msg = "section créée";
        this.afficherSections();
      }
    });
  }

  modifierSection() {
    this.sectionService.updateSection(this.section, () => {
      this.msg = "section modifiée";
      this.afficherSections();
    });
  }

  modifier(index: number) {
    this.sectionService.savedSection = this.sections[index];
    this.section = this.sectionService.savedSection;
  }

  supprimer(index: number) {
    this.sectionService.deleteSection(this.sections[index].id_section, () => {
      this.afficherSections();
      this.msg = "section supprimée";
    });
  }
}



