import { Injectable } from '@angular/core';
import { Component } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { HomeComponent } from '../components/home/home.component';
import { Observable } from 'rxjs';
import { Subject } from 'rxjs/Subject';

@Injectable()
export class LoginService {

  logged: boolean;
  private subject = new Subject<any>();

  constructor(private http: HttpClient) {
    //if(window.localStorage.getItem)
  }

  login(username, password, next) {
    this.http.post('http://localhost:5000/app/login'/*window.location.origin+'/login'*/, { username: username, password: password }).subscribe(response => {
      /*if (Object.keys(response).length == 0) {
        this.sendMessage("Erreur de Nom d'utilisateur ou mot de passe erroné...");
      }*/
      if (Object.keys(response).length > 0) {
        var converted: any;
        converted = response;
        console.log(response);
        localStorage.setItem('token', converted.token);
        localStorage.setItem('user', converted.user);
        localStorage.setItem('id_role', converted.id_role);
        console.log(response);
        this.logged = true;
        next(this.logged);
        this.sendMessage("Bienvenue " + converted.user);
      }
      else {
        this.logged = false;
        console.log("not connected " + response);
        this.sendMessage("Erreur de Nom d'utilisateur ou mot de passe erroné...");
      }
    }, error => {
      console.log(error);
      this.logged = false;
      next(this.logged);
    });
  }

  checkAuth() {
    let token = window.localStorage.getItem("token");
    this.http.post('http://localhost:5000/app/', { token: token }).subscribe(response => { });
  }

  logout() {
    localStorage.removeItem('token');
    localStorage.removeItem('user');
    localStorage.removeItem('id_role');
    this.logged = false;
    this.sendMessage("Veuillez vous connecter...");
  }

  sendMessage(message) {
    this.subject.next({ text: message, logged: this.logged });
  }

  clearMessage() {
    this.subject.next();
  }

  getMessage(): Observable<any> {
    return this.subject.asObservable();
  }

  getLog() {
    if (window.localStorage.getItem('token')!=null) { return this.logged = true; }
    else { return this.logged = false; }
  }
}



