import { Component, Input } from '@angular/core';
import { PaysService } from '../../../../services/pays.service';
import { Pays } from '../../../../models/pays';

@Component({
  selector: 'pays',
  templateUrl: './pays.html'
})

export class DonneesPaysComponent {
  paysTab: Pays[];
  pays = new Pays();

  constructor(private paysService: PaysService) {
    this.paysService.getAllPays((data) => { this.paysTab = data; });
    this.afficherPays();
  }

  createPays() {
    this.paysService.createPays(this.pays.nom_pays, () => { this.afficherPays(); });
  }

  afficherPays() {
    this.paysService.getAllPays((data) => {
      console.log(data);
      this.paysTab = data;
    });
  }

  onSubmit() {
    //this.pays.nom_pays = this.nom;
    console.log(this.pays.nom_pays);
    this.createPays();
  }

  modifier(index: number) {
    this.pays = this.paysTab[index];
  }

  update() {
    this.paysService.updatePays(this.pays, () => { this.afficherPays(); });
  }

  delete(index: number) {
    this.paysService.deletePays(this.paysTab[index], () => { this.afficherPays(); })
  }
}



