import { Injectable } from '@angular/core';
import { RequestService } from './request.service';
import { HttpClient } from '@angular/common/http';
import { Module } from '../models/module';
import { Matiere } from '../models/matiere';
import { MatiereService } from '../services/matiere.service';
import { AContient } from '../models/aContient';
import { AContientService } from '../services/a.contient.service';

@Injectable()
export class ModuleService {

  request: string;
  savedModule: Module;
  saved = false;

  constructor(
    private requestService: RequestService
  ) { }

  keepThisModule(module: Module) {
    this.savedModule = module;
  }

  getActualModule() {
    return this.savedModule;
  }

  getAllModules(next) {
    var url = 'http://localhost:5000/app/module/';
    this.requestService.getRequestByUrl(url, (data) => {
      next(data);
    });
  }

  getModuleByCode(codeModule: string, next) {
    var url = 'http://localhost:5000/app/module/code/' + codeModule;
    this.requestService.getRequestByUrl(url, (data) => {
      next(data);
    });
  }


  getModuleByNo(noModule: string, next) {
    let data: any[];
    var url = 'http://localhost:5000/app/module/no/' + noModule;
    
    this.requestService.getRequestByUrl(url, (module) => {
      next(module);
    });
  }

  createModule(module: Module, next) {

    this.request = "INSERT INTO module VALUES('" +
      module.code_UF + "', '" +
      module.no_UF + "', '" +
      module.intitule + "', '" +
      module.nb_periodes + "', '" +
      module.niv_enseignement + "', '" +
      module.niv_UF + "');";
    
    var url = "http://localhost:5000/app/module/create";
    this.requestService.postRequest(url, this.request, (rep) => { next(rep); });
    //this.aContientService.createAContient(aContient, next);
  }

  deleteModule(module: Module, next) {

    //this.aContientService.deleteAContient()
    let requestAContient = "DELETE FROM a_contient WHERE code_UF=" + module.code_UF + ";";
    this.request = "DELETE FROM module WHERE code_UF='" + module.code_UF + "';";

    var url = "http://localhost:5000/app/module/delete/aContient";
    this.requestService.postRequest(url, requestAContient, (rep1) => {
      var url = "http://localhost:5000/app/module/delete";
      this.requestService.postRequest(url, this.request, (rep2) => { next(rep2); });
    });
  }

  updateModule(module: Module, next) {
    this.request = "UPDATE module SET " +
      "code_UF = '" + module.code_UF + "'," +
      "no_UF = '" + module.no_UF + "'," +
      "intitule = '" + module.intitule + "'," +
      "nb_periodes = '" + module.nb_periodes + "'," +
      "niv_enseignement = '" + module.niv_enseignement + "'," +
      "niv_UF = '" + module.niv_UF + "' " +
      "WHERE code_UF = '" + module.code_UF + "';";

    var url = "http://localhost:5000/app/module/update";
    this.requestService.postRequest(url, this.request, next);
  }

  triParCodeUF(modules: Array<Module>) {
    let moduleTri = new Array<Module>();
    //recherche par min
    let min = new Module();
    let save = new Module();
    for (let i = 0; i < modules.length; i++) {
      for (let j = i; j < modules.length; j++) {
        min = modules[i];
        if (parseInt(modules[j].code_UF) <= parseInt(min.code_UF)) {
          save = min;
          min = modules[j];
          modules[j] = save;
        }
        modules[i] = min;
      }
    }
    moduleTri = modules;
    return moduleTri;
    // Tester si le tri a fonctionné...
  }

}



