import { Injectable } from '@angular/core';
import { RequestService } from './request.service';
import { HttpClient } from '@angular/common/http';
import { Module } from '../models/module';
import { Matiere } from '../models/matiere';
import { PaysService } from './pays.service';
import { AContient } from '../models/aContient';
import { ModuleService } from '../services/module.service';
import { MatiereService } from '../services/matiere.service';


// MODULE AND MATIERE
@Injectable()
export class AContientService {

  aContientTab = new Array<AContient>();
  aContient: AContient = new AContient();
  oldModule = new Module();
  request: string;
  savedAContient: AContient;
  saved = false;

  constructor(
    private requestService: RequestService,
    private matiereService: MatiereService,
    private moduleService: ModuleService
  ) { }


  getAllAContient(next) {
    let returnData = function (data) {
      next(data);
    };
    let aContientTab = new Array<AContient>();
    let aContient = new AContient();
    var url = 'http://localhost:5000/app/a-contient';
    this.requestService.getRequestByUrl(url, (data) => {
      let i = 0;
      for (let line of data) {
        console.log(line.code_UF);
        this.constructAContient(line.code_UF, line.id_matiere, (contient) => {
          if (line.code_UF == contient.module.code_UF) {
            console.log("code " + contient.module.code_UF);
            aContientTab.push(contient);
            console.log("acompose ");
          }
          i++;
          if (i === Object.keys(data).length) {
            next(this.aContientTab);
          }
        });
      }
    });

  }
  getModuleByCode(codeUF: string, next) {
    this.moduleService.getModuleByCode(codeUF, (module) => {
      next(module);
    });
  }

  getMatiereById(id: number, next) {
    this.matiereService.getMatiereById(id, (matiere) => {
      next(matiere);
    });
  }

  async getAContientByModule(code: string, next) {
    var url = 'http://localhost:5000/app/a-contient/module/' + code;
    await this.requestService.getRequestByUrl(url, async (data) => {
      //this.aCompose = data;
      let aContientTab = new Array<AContient>();
      if (Object.keys(data).length > 0) {
          for (let i = 0; i < Object.keys(data).length; i++) {
            this.constructAContient(data[i].code_UF, data[i].id_matiere, async (aContient) => {
              aContientTab[i] = new AContient();;
              aContientTab[i] = aContient;
              console.log("passage acontient" + aContientTab[i].matiere.intitule);
                next(aContientTab);
            });
        }
      }
    });
  }

  getAContientByMatiere(id: number, next) {
    var url = 'http://localhost:5000/app/a-contient/matiere/' + id;
    this.requestService.getRequestByUrl(url, (data) => {
      this.aContient = data;
      next(data);
    });
  }

  async constructAContient(codeUF: string, idMatiere: number, next) {
    let aContient = new AContient();
    await this.getModuleByCode(codeUF, (module) => {
      aContient.module = new Module();
      aContient.module = module[0];
      this.getMatiereById(idMatiere, (matiere) => {
        aContient.matiere = new Matiere();
        aContient.matiere = matiere;
        this.aContientTab.push(aContient);
        next(aContient);
      });
    });
  }

  constructAllAContient(next) {
    let aContientTab = new Array<AContient>();
    let aContient = new AContient();
    var url = 'http://localhost:5000/app/a-contient';
    this.requestService.getRequestByUrl(url, async (data) => {
      let i = 0;
      for (let i = 0; i < Object.keys(data).length; i++) {
        aContient = new AContient();
        aContient.module = new Module();
        aContient.matiere = new Matiere();
        this.constructAContient(data[i].code_UF, data[i].id_matiere, (acontient) => { aContientTab.push(acontient); });
      }
      next(aContientTab);
    });
  }


  createAContient(aContient: AContient, next) {
    this.request = "INSERT INTO a_contient VALUES('" +
      aContient.module.code_UF + "', " + aContient.matiere.id_matiere + ");";
    var url = "http://localhost:5000/app/a-contient/create";
    this.requestService.postRequest(url, this.request, (response) => {
      next(response);
    });
  }



  updateAContient(aContient: AContient, oldAContient: AContient, next) {
    console.log("code " + oldAContient.module.code_UF);
    this.request = "UPDATE a_contient SET" +
      " id_matiere=" + aContient.matiere.id_matiere +
      ", code_UF=" + aContient.module.code_UF +
      " WHERE id_matiere =" + oldAContient.matiere.id_matiere +
      " AND code_UF='" + oldAContient.module.code_UF + "';";

    var url = "http://localhost:5000/app/a-contient/update";
    this.requestService.postRequest(url, this.request, (response) => {
      next(response);
    });
  }

  updateOrCreateAContient(aContient: AContient, next) {
    if (this.saved == true) {
      this.updateAContient(aContient, this.savedAContient, () => {
        console.log("maj acontient");
        next();
      });
    }
    else {
      this.createAContient(aContient, () => {
        console.log("acontient créé");
        next();
      })
    }
  }

  deleteAContient(aContient, next) {
    this.request = "DELETE FROM a_contient WHERE" +
      " id_matiere=" + aContient.matiere.id_matiere +
      " AND code_UF='" + aContient.module.code_UF + "';";
    var url = "http://localhost:5000/app/a-contient/delete";
    this.requestService.postRequest(url, this.request, (data) => {
      next();
    });
  }

}



