import { Ville } from './ville';
import { Pays } from './pays';

export class Etudiant {

  id_etudiant: number;
  nom: string;
  prenom: string;
  genre: string;
  date_naiss: string;
  adresse: string;
  code_postal: string;
  nationalite: string;
  telephone: string;
  numero_national: string;
  validite_CI: boolean;
  email: string;
  infos_utiles: string;
  ville: Ville;
  ville_naissance: Ville;

  constructor() {
    this.nom = "";
    this.prenom = "";
    this.genre = "";
    this.date_naiss = "";
    this.adresse = "";
    this.code_postal = "";
    this.nationalite = "";
    this.telephone = "";
    this.numero_national = "";
    this.validite_CI = false;
    this.email = "";
    this.genre = "";
    this.infos_utiles = "";
    this.ville = new Ville();
    this.ville.nom_ville = "";
    this.ville.pays = new Pays();
    this.ville.pays.nom_pays = "Belgique";
    this.ville_naissance = new Ville();
    this.ville_naissance.nom_ville = "";
    this.ville_naissance.pays = new Pays();
    this.ville_naissance.pays.nom_pays = "";
  }
}



