import { Component, Input } from '@angular/core';
import { HoraireService } from '../../../../services/horaire.service';
import { Horaire } from '../../../../models/horaire';

@Component({
  selector: 'plage-horaire',
  templateUrl: './horaire.html'
})

export class DonneesHorairesComponent {

  horaires: Horaire[];
  horaire = new Horaire();

  constructor(private horaireService: HoraireService) { this.afficherHoraires(); }

  createHoraire() {
    this.horaireService.createHoraire(this.horaire, () => { this.afficherHoraires(); });
  }

  afficherHoraires() {
    this.horaireService.getAllHoraires((data) => {
      console.log(data);
      this.horaires = data;
    })
  }

  onSubmit() {
    this.createHoraire();
  }

  modifier(index: number) {
    this.horaire = this.horaires[index];
    console.log(this.horaires[index]);
    this.horaireService.cutString(this.horaires[index], (data) => {
      this.horaire = data;
    });
  }

  update() {
    this.horaireService.updateHoraire(this.horaire, () => { this.afficherHoraires(); })
  }
}



